<template>
  <section class="lineChart">
    <div class="title">
      <span class="contentName">{{title}}</span>
    </div>
    <div :id="container" class="container" ref="reference"></div>
  </section>
</template>

<script>
// 定时器
// import setIntervalObject from '@/mixins/setIntervalObject'

// 图表基础引用
import ChartBase from '@/mixins/ChartBase'
import { Chart } from '@antv/g2'
export default {
  mixins: [ChartBase],
  name: 'lineChart',
  data () {
    return {
      legendLength: 0
    }
  },
  props: {
    sourDate: {
      required: false,
      default: () => ({}),
      type: Object
    },
    theUrl: {
      required: false,
      default: '/api/lineDate',
      type: String
    }
  },
  computed: {
    title () {
      return this.sourDate.title ? this.sourDate.title : ''
    },
    showType () {
      return this.sourDate.type ? this.sourDate.type : 'line'
    }
  },
  watch: {
    sourDate: {
      handler (newName, oldName) {
        if (newName.result !== undefined && newName.result.length > 0) {
          this.charDate = newName.result
          this.updatedCharts()
        }
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    updatedCharts () {
      if (this.charDate.length > 0) {
        try {
          let sour = JSON.parse(JSON.stringify(this.charDate))
          if (this.chartOb) {
            if (this.showType !== 'line') {
              sour.sort((a, b) => {
                return b.value - a.value
              })
            }
            this.chartOb.changeData(sour)
          } else {
            this.createdChart()
          }
        } catch (e) {
          this.createdChart()
        }
      } else {
        console.error('图表数据为空')
      }
    },
    createdChart () {
      try {
        this.chartOb.clear()
      } catch (e) {
        // console.log('尝试清空图表')
      }
      try {
        if (this.charDate.length > 0) {
          this.chartOb = new Chart({
            container: this.$refs.reference,
            padding: [200, 120, 85, 250],
            autoFit: true,
            height: 640
          })
          let sour = JSON.parse(JSON.stringify(this.charDate))
          if (this.showType !== 'line') {
            sour.sort((a, b) => {
              return b.value - a.value
            })
          }
          this.chartOb.data(sour)
          let legendItem = []
          let newsMap = new Map()
          sour.map(item => {
            newsMap.set(item.indicateName, item.value)
            if (!legendItem.includes(item.indicateName)) {
              legendItem.push(item.indicateName)
            }
          })
          this.legendLength = legendItem.length
          this.chartOb.axis('value', {
            grid: null,
            label: {
              autoRotate: false,
              offset: 35,
              rotate: 0,
              style: {
                fontSize: 30,
                fill: '#6ecaff'
              },
              formatter: value => {
                return value
              }
            },
            line: {
              style: this.lineStyle
            }
          })
          this.chartOb.axis('dateTime', {
            grid: null,
            label: {
              offset: 45,
              style: {
                fontSize: 30,
                textAlign: 'center',
                textBaseline: 'middle',
                fill: '#6ecaff'
              }
            },
            line: {
              style: this.lineStyle
            }
          })
          this.chartOb.scale('dateTime', {
            type: 'time',
            tickCount: 5,
            mask: 'HH:mm'
          })
          this.chartOb.scale('value', {
            min: 0,
            tickCount: 5
          })
          this.chartOb.tooltip({
            showCrosshairs: true,
            shared: true,
            offset: 10, // tooltip 距离鼠标的偏移量
            containerTpl: '<div class="g2-tooltip" style="font-size:35px;">' +
    '<div class="g2-tooltip-title" style="margin:10px 0;font-size:35px;padding:15px 0;"></div>' +
    '<div class="g2-tooltip-list" style="font-size:35px;width:100%;"></div></div>', // tooltip 容器模板
            itemTpl: '<div style="font-size:30px;width:100%;height:45px;display:flex;" data-index={index}><span style="background-color:{color};width:20px;height:20px;display:inline-block;margin-right:16px;"></span>{name}: {value}</div>', // tooltip 每项记录的默认模板
            inPlot: true, // 将 tooltip 展示在指定区域内
            follow: true
          })

          let colors = [
            'l(270) 0:#5586ff 1:#37d7ff',
            'l(270) 0:#fe9844 1:#ffe535'
          ]
          let itemsList = legendItem.map((value, index) => {
            return {
              name: value, // 对应 itemName
              value: value, // 对应 itemValue
              marker: {
                symbol: 'square', // marker 的形状
                style: {
                  r: 20, // marker 图形半径
                  fill: colors[index % colors.length] // marker 颜色，使用默认颜色，同图形对应
                }
              }
            }
          })
          this.chartOb.legend('indicateName', {
            position: 'top',
            offsetY: 130,
            itemWidth: 300,
            layout: 'horizontal',
            items: itemsList,
            itemName: {
              style: {
                fontSize: 30,
                fill: '#6ecaff'
              }
            }
          })
          if (this.legendLength < 2) {
            this.chartOb.legend(false)
          }
          if (this.showType === 'line') {
            this.chartOb
              .area()
              .position('dateTime*value')
              .color('group', colors)
              .shape('smooth')
            this.chartOb
              .line().size(8)
              .position('dateTime*value')
              .color('group', colors)
              .shape('smooth')
          } else {
            this.chartOb.legend(false)
            this.chartOb.axis('group', {
              grid: null,
              label: {
                offset: 45,
                style: {
                  fontSize: 30,
                  textAlign: 'center',
                  textBaseline: 'middle',
                  lineHeight: '35',
                  fill: '#6ecaff'
                },
                formatter: value => {
                  let breakWork = '\n'
                  let name = value.replace('营业部', '')
                  return name.length > 4 ? name.slice(0, 4) + breakWork + name.slice(4, name.length) : name
                }
              },
              line: {
                style: this.lineStyle
              }
            })
            this.chartOb
              .interval()
              .position('group*value')
              .color('indicateName', colors)
              .adjust([
                {
                  type: 'dodge',
                  marginRatio: 0
                }
              ])
          }

          this.chartOb.interaction('active-region')
          this.chartOb.render()
        } else {

        }
      } catch (e) {
        console.error(JSON.stringify(e))
      }
    }
  }
}
</script>

<style scoped>
.lineChart{
  position: relative;
}
.container {
  width: 1180px;
  background: transparent;
  margin: 10px auto;
  position: relative;
}
.title{
  position: absolute;
  width: 100%;
  top: 25px;
  left: 0;
}
.contentName {
  font-size: 64px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 96px;
  letter-spacing: 0px;
  color: #e0e8ff;
}
</style>



