/**
 * 模块说明
 * @module websock模块
 *
 */
const websockObject = {
  data () {
    return {
      websock: null,
      lockReconnect: false,
      timeout: 5 * 60 * 1000, // 30秒一次心跳
      timeoutObj: null, // 心跳心跳倒计时
      serverTimeoutObj: null, // 心跳倒计时
      timeoutnum: null, // 断开 重连倒计时
      // eslint-disable-next-line no-undef
      wsuri: BaseHost.websockUrl,
      leave: false,
      fristBack: true
    }
  },
  watch: {
    wsuri: {
      handler (newName, oldName) {
        if (newName !== '') {
          if (this.websock !== null) {
            this.websock.close()
          }
          let _this = this
          this.$nextTick(() => {
            _this.initWebSocket()
          })
        }
      },
      immediate: true,
      deep: true
    }
  },
  created () {
    this.initWebSocket()
  },
  beforeDestroy () {
    console.log(this.wsuri + 'to close')
    this.wsuri = ''
    this.leave = true
    clearTimeout(this.timeoutObj)
    clearTimeout(this.serverTimeoutObj)
    this.websock && this.websock.close() // 离开路由之后断开websocket连接
  },
  methods: {
    initWebSocket () { // 初始化weosocket
      this.fristBack = true
      const wsuri = JSON.parse(JSON.stringify(this.wsuri))
      let isFullUrl = this.wsuri.replace('ws://' + window.BaseHost.host, ' ')
      if (wsuri !== '' && isFullUrl !== '') {
        this.websock = new WebSocket(wsuri)
        this.websock.onmessage = this.websocketonmessage
        this.websock.onopen = this.websocketonopen
        this.websock.onerror = this.websocketonerror
        this.websock.onclose = this.websocketclose
      }
      this.useTheTestDate()
    },
    websocketonopen () { // 连接建立之后执行send方法发送数据
      console.log(this.wsuri + 'on Open')
      this.websocketsend('admin')
      // this.fristBack = true
      this.start()
    },
    websocketonerror () { // 连接建立失败重连
      console.log(this.wsuri + 'on Error')
      this.websocketreconnect()
      // if (process.env.NODE_ENV === 'development') {
      //   this.useTheTestDate()
      // }
    },
    websocketonmessage (e) { // 数据接收
      this.reset()
      if (e.data === 'ok') {
        return false
      }
      const data = JSON.parse(e.data)
      const redata = Array.isArray(data) ? data : [data]
      if (redata.length > 0) {
        try {
          this.getWebsocketMsg(redata).then(() => {
            this.fristBack = false
          })
        } catch (error) {
          console.error(error)
        }
      }
    },
    websocketsend (Data) { // 数据发送
      this.websock.send(Data)
    },
    websocketclose (e) { // 关闭
      console.log('断开连接', e)
      this.lockReconnect = false
      if (!this.leave) this.websocketreconnect()
    },
    websocketreconnect () { // 重新连接
      console.log(this.wsuri + 'on Reconnect')
      var that = this
      if (that.lockReconnect) {
        return false
      } else {
        // 没连接上会一直重连，设置延迟避免请求过多
        that.timeoutnum && clearTimeout(that.timeoutnum)
        that.timeoutnum = setTimeout(function () {
          // 新连接
          that.initWebSocket()
          that.lockReconnect = true
        }, 10000)
      }
    },
    reset () { // 重置心跳
      var that = this
      // 清除时间
      clearTimeout(that.timeoutObj)
      clearTimeout(that.serverTimeoutObj)
      // 重启心跳
      that.start()
    },
    start () { // 开启心跳
      var self = this
      self.timeoutObj && clearTimeout(self.timeoutObj)
      self.serverTimeoutObj && clearTimeout(self.serverTimeoutObj)
      self.timeoutObj = setTimeout(function () {
        // 这里发送一个心跳，后端收到后，返回一个心跳消息，
        if (self.websock.readyState === 1) { // 如果连接正常
          self.websock.send('cheack')
        } else { // 否则重连
          self.websocketreconnect()
        }
        self.serverTimeoutObj = setTimeout(function () {
          console.log('超时关闭')
          self.websock.close()
        }, self.timeout + 5 * 1000)
      }, self.timeout)
    }
  }
}

export default websockObject



