<script>
import backgroundImage from "@/assets/image/background/1-2.jpg";
import runningList from "./com/runningList";
// 定时器
// import setIntervalObject from '@/mixins/setIntervalObject'

import websockObject from "@/mixins/websock";

// 图表基础引用
import ChartBase from "@/mixins/ChartBase";
import { Chart } from "@antv/g2";

const baseHeader = ["市场名称", "成交笔数", "委托笔数", "废单数量"];
export default {
  name: "leftCenter",
  mixins: [ChartBase, websockObject],
  data() {
    return {
      backgroundImage,
      baseHeader,
      legendItem: [],
      KcmimHeader: JSON.parse(JSON.stringify(baseHeader)),
      KcmimDeaLindicate: [],
      FinanceHeader: JSON.parse(JSON.stringify(baseHeader)),
      FinanceDeaLindicate: [],
      charDateMap: new Map()
    };
  },
  components: {
    runningList
  },
  mounted() {
    let _this = this;
    // eslint-disable-next-line no-undef
    _this.wsuri = BaseUrl.leftCenter.websockUrl;
    // this.useTheTestDate()
  },
  methods: {
    useTheTestDate() {
      let _this = this;
      let num = 0;
      setInterval(() => {
        let tillTiem = new Date().getTime();
        let m = 10000;
        let n = 0;
        let test = [
          {
            dateTime: 1602316632733,
            type: "line",
            value: Math.floor(
              Math.random() * (1000000000 - 100000000) + 100000000
            ),
            indicateName: "集中交易",
            group: "10-10"
          },
          {
            dateTime: 1602402987811,
            type: "line",
            value: Math.floor(
              Math.random() * (1000000000 - 100000000) + 100000000
            ),
            indicateName: "集中交易",
            group: "10-11"
          },
          {
            dateTime: 1602490925347,
            type: "line",
            value: Math.floor(
              Math.random() * (1000000000 - 100000000) + 100000000
            ),
            indicateName: "集中交易",
            group: "10-12"
          },
          {
            dateTime: 1602575454304,
            type: "line",
            value: Math.floor(
              Math.random() * (1000000000 - 100000000) + 100000000
            ),
            indicateName: "集中交易",
            group: "10-13"
          },
          {
            dateTime: 1602664153485,
            type: "line",
            value: Math.floor(
              Math.random() * (1000000000 - 100000000) + 100000000
            ),
            indicateName: "集中交易",
            group: "10-14"
          },
          {
            dateTime: 1602402987652,
            type: "line",
            value: Math.floor(
              Math.random() * (1000000000 - 100000000) + 100000000
            ),
            indicateName: "融资融券",
            group: "10-11"
          },
          {
            dateTime: 1602490924606,
            type: "line",
            value: Math.floor(
              Math.random() * (1000000000 - 100000000) + 100000000
            ),
            indicateName: "融资融券",
            group: "10-12"
          },
          {
            dateTime: 1602575450486,
            type: "line",
            value: Math.floor(
              Math.random() * (1000000000 - 100000000) + 100000000
            ),
            indicateName: "融资融券",
            group: "10-13"
          },
          {
            dateTime: 1602661788394,
            type: "line",
            value: Math.floor(
              Math.random() * (1000000000 - 100000000) + 100000000
            ),
            indicateName: "融资融券",
            group: "10-14"
          },
          {
            dateTime: tillTiem,
            type: "table",
            value: Math.floor(Math.random() * (m - n) + n),
            indicateName: "成交笔数",
            group: "集中交易",
            example: "上海证券交易所" + num
          },
          {
            dateTime: tillTiem,
            type: "table",
            value: Math.floor(Math.random() * (m - n) + n),
            indicateName: "委托笔数",
            group: "集中交易",
            example: "上海证券交易所" + num
          },
          {
            dateTime: tillTiem,
            type: "table",
            value: Math.floor(Math.random() * (m - n) + n),
            indicateName: "废单数量",
            group: "集中交易",
            example: "上海证券交易所" + num
          },
          {
            dateTime: tillTiem,
            type: "table",
            value: Math.floor(Math.random() * (m - n) + n),
            indicateName: "成交笔数",
            group: "融资融券",
            example: "香港交易所" + num
          },
          {
            dateTime: tillTiem,
            type: "table",
            value: Math.floor(Math.random() * (m - n) + n),
            indicateName: "委托笔数",
            group: "融资融券",
            example: "香港交易所" + num
          },
          {
            dateTime: tillTiem,
            type: "table",
            value: Math.floor(Math.random() * (m - n) + n),
            indicateName: "废单数量",
            group: "融资融券",
            example: "香港交易所" + num
          }
        ];
        num++;
        _this.getWebsocketMsg(test);
      }, 1000);
    },
    getWebsocketMsg(sourDate) {
      let self = this;
      if (sourDate.length > 0) {
        if (self.fristBack) {
          /**
           * 针对第一次连接时可能存在线段数据全量更新
           */
          let charBaseDate = [];
          sourDate.map(sourDateItem => {
            if (sourDateItem.type !== "table") {
              sourDateItem.value = sourDateItem.value / 10000;
              let targetKey =
                sourDateItem.group + "#" + sourDateItem.indicateName;
              charBaseDate.push(sourDateItem);
              self.charDateMap.set(targetKey, charBaseDate.length - 1);
            } else {
              let fixArry = [];
              fixArry.push(sourDateItem);
              if (sourDateItem.group === "集中交易") {
                self.setTheKcmimDeaLindicate(sourDateItem.example, fixArry);
              } else {
                self.setTheFinanceDeaLindicate(sourDateItem.example, fixArry);
              }
            }
          });
          self.charDate = charBaseDate;
          this.$nextTick(() => {
            self.updatedCharts();
          });
        } else {
          let toUpdatedCharts = false;
          for (let sourDateItem of sourDate) {
            if (sourDateItem.type !== "table") {
              let targetKey =
                sourDateItem.group + "#" + sourDateItem.indicateName;
              let target = self.charDateMap.has(targetKey)
                ? self.charDateMap.get(targetKey)
                : -1;
              sourDateItem.value = sourDateItem.value / 10000;
              if (sourDateItem.value < 10) {
                console.log(sourDateItem);
              }
              toUpdatedCharts = true;
              if (target !== -1) {
                self.$set(self.charDate, target, sourDateItem);
              } else {
                if (!self.legendItem.includes(sourDateItem.indicateName)) {
                  self.legendItem.push(sourDateItem.indicateName);
                }
                self.charDate.push(sourDateItem);
                self.charDateMap.set(targetKey, self.charDate.length - 1);
                self.charDate.sort((a, b) => a.dateTime - b.dateTime);
                if (self.charDate.length > self.legendItem.length * 20) {
                  let del = self.charDate.splice(0, 2);
                  del.forEach(delitem => {
                    let delKey = delitem.group + "#" + delitem.indicateName;
                    self.charDateMap.del(delKey);
                  });
                }
              }
            } else {
              let fixArry = [];
              fixArry.push(sourDateItem);
              if (sourDateItem.group === "集中交易") {
                self.setTheKcmimDeaLindicate(sourDateItem.example, fixArry);
              } else {
                self.setTheFinanceDeaLindicate(sourDateItem.example, fixArry);
              }
            }
          }
          if (toUpdatedCharts) {
            this.$nextTick(() => {
              self.updatedCharts();
            });
          }
        }
      }
      self.fristBack = false;
      return new Promise(function(resolve, reject) {
        resolve();
      });
    },
    setTheFinanceDeaLindicate(key, value) {
      let target = this.FinanceDeaLindicate.findIndex(item => {
        return item.group === key;
      });
      if (target === -1) {
        if (Array.isArray(value)) {
          value.sort(
            (a, b) =>
              this.FinanceHeader.indexOf(a.indicateName) -
              this.FinanceHeader.indexOf(a.indicateName)
          );
        } else {
          let fix = [];
          fix.push(value);
          value = fix;
        }
        this.FinanceDeaLindicate.push({
          group: key,
          result: value
        });
      } else {
        let setValue = [];
        let base = JSON.parse(
          JSON.stringify(this.FinanceDeaLindicate[target].result)
        );
        value.forEach(valueItem => {
          let nodeTarget = base.findIndex(item => {
            return item.indicateName === valueItem.indicateName;
          });
          if (nodeTarget === -1) {
            base.push(valueItem);
          } else {
            base[nodeTarget] = valueItem;
          }
        });
        setValue = base;
        setValue.sort(
          (a, b) =>
            this.FinanceHeader.indexOf(a.indicateName) -
            this.FinanceHeader.indexOf(a.indicateName)
        );
        this.$set(this.FinanceDeaLindicate, target, {
          group: key,
          result: setValue
        });
      }
    },
    setTheKcmimDeaLindicate(key, value) {
      let target = this.KcmimDeaLindicate.findIndex(item => {
        return item.group === key;
      });
      if (target === -1) {
        if (Array.isArray(value)) {
          value.sort(
            (a, b) =>
              this.KcmimHeader.indexOf(a.indicateName) -
              this.KcmimHeader.indexOf(a.indicateName)
          );
        } else {
          let fix = [];
          fix.push(value);
          value = fix;
        }
        this.KcmimDeaLindicate.push({
          group: key,
          result: value
        });
      } else {
        let setValue = [];
        let base = JSON.parse(
          JSON.stringify(this.KcmimDeaLindicate[target].result)
        );
        value.forEach(valueItem => {
          let nodeTarget = base.findIndex(item => {
            return item.indicateName === valueItem.indicateName;
          });
          if (nodeTarget === -1) {
            base.push(valueItem);
          } else {
            base[nodeTarget] = valueItem;
          }
        });
        setValue = base;
        setValue.sort(
          (a, b) =>
            this.KcmimHeader.indexOf(a.indicateName) -
            this.KcmimHeader.indexOf(a.indicateName)
        );
        this.$set(this.KcmimDeaLindicate, target, {
          group: key,
          result: setValue
        });
      }
    },
    createdChart() {
      try {
        this.chartOb.clear();
      } catch (e) {
        // console.log('尝试清空图表')
      }
      try {
        if (this.charDate.length > 0) {
          this.chartOb = new Chart({
            container: this.$refs.reference,
            padding: [180, 130, 80, 180],
            autoFit: true,
            height: 610
          });
          let sour = JSON.parse(JSON.stringify(this.charDate));
          this.chartOb.data(sour);
          this.chartOb.axis("value", {
            grid: null,
            label: {
              offset: 55,
              rotate: 0,
              style: this.xStyle
            },
            title: {
              position: "end",
              autoRotate: false,
              offset: 50,
              rotate: 0,
              style: {
                textAlign: "middle", // 文本对齐方向，可取值为： start middle end
                fill: "#6ecaff", // 文本的颜色
                fontSize: "30", // 文本大小
                lineHeight: "35",
                fontWeight: "bold", // 文本粗细
                textBaseline: "bottom"
              }
            },
            line: {
              style: this.lineStyle
            }
          });
          this.chartOb.axis("group", {
            grid: null,
            label: {
              offset: 20,
              style: this.yStyle
            },
            title: {
              position: "end",
              autoRotate: false,
              offset: 0,
              rotate: 0,
              style: {
                textAlign: "start", // 文本对齐方向，可取值为： start middle end
                fill: "#6ecaff", // 文本的颜色
                fontSize: "30", // 文本大小
                fontWeight: "bold", // 文本粗细
                textBaseline: "bottom"
              }
            },
            line: {
              style: this.lineStyle
            }
          });
          this.chartOb.scale("group", {
            tickCount: 10,
            alias: "   " + "时间"
          });
          this.chartOb.scale("value", {
            min: 0,
            tickCount: 5,
            alias: "交易量(万元)" + "\n"
          });
          this.chartOb.tooltip({
            showCrosshairs: true,
            shared: true
          });
          this.chartOb.legend(false);
          let colors = [
            "l(270) 0:#2bc1ff 1:#2bc1ff",
            "l(270) 0:#fe9844 1:#ffe535"
          ];
          // this.chartOb
          //   .area()
          //   .position('group*value')
          //   .color('indicateName', colors)
          //   .shape('smooth')
          this.chartOb
            .line()
            .size(4)
            .position("group*value")
            .color("indicateName", colors)
            .shape("smooth");

          this.chartOb.interaction("active-region");
          this.chartOb.render();
          this.inited = true;
        } else {
          this.inited = false;
        }
        this.setTheInterval();
      } catch (e) {
        this.inited = false;
        console.error(JSON.stringify(e));
      }
    }
  },
  render(h) {
    return (
      <section
        class="mainBox"
        {...{
          style: { backgroundImage: " url(" + this.backgroundImage + ")" }
        }}
      >
        <section class="contentBox">
          <section class="mainChartBox">
            <div class="title fixTitle">前20交易日每日成交总金额</div>
            <div
              id="{this.container}"
              class="container"
              ref="reference"
              {...{ style: { minHeight: "610px" } }}
            ></div>
          </section>

          <section class="tableBox">
            <section class="mainChartBox">
              <div class="title">金证集中交易系统各个交易市场</div>
              <section class="dealContent">
                <div class="dealContentHeader dealContentRow">
                  {this.KcmimHeader.map(item => {
                    return (
                      <div class="dealContentHeaderItem dealContentItem">
                        {item}
                      </div>
                    );
                  })}
                </div>
                <div class="dealContentMain">
                  <runningList
                    sourDate={this.KcmimDeaLindicate}
                    headerDate={this.KcmimHeader}
                    contentHeight={345}
                    overflow="hidden"
                    runnring={true}
                  ></runningList>
                </div>
              </section>
            </section>
            <section class="mainChartBox" {...{ style: { width: "805px" } }}>
              <div class="title">金证融资融券系统各个交易市场</div>
              <section class="dealContent">
                <div class="dealContentHeader dealContentRow">
                  {this.FinanceHeader.map(item => {
                    return (
                      <div class="dealContentHeaderItem dealContentItem">
                        {item}
                      </div>
                    );
                  })}
                </div>
                <div class="dealContentMain">
                  <runningList
                    sourDate={this.FinanceDeaLindicate}
                    headerDate={this.FinanceHeader}
                    contentHeight={345}
                    overflow="hidden"
                    runnring={true}
                  ></runningList>
                </div>
              </section>
            </section>
          </section>
        </section>
      </section>
    );
  }
};
</script>

<style scoped>
.contentBox {
  position: absolute;
  top: 0;
  left: 94px;
  width: 1705px;
  height: 1080px;
}
.tableBox {
  width: 1660px;
  margin: auto;
  display: flex;
  justify-content: space-between;
}
.tableBox .mainChartBox {
  width: 812px;
  height: 415px;
  overflow: hidden;
}
.tableBox .mainChartBox .title {
  position: relative;
  left: 0;
  top: 0;
  text-align: left;
  line-height: 1;
  padding: 19px 0;
}
.dealContentRow {
  display: flex;
  color: #b4ddff;
}
.dealContentHeaderItem {
  font-size: 32px;
}
.dealContentItem {
  height: 86px;
  width: 185px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.dealContentItem:first-child {
  width: 280px;
}
.dealContentHeader {
  height: 81px;
}
.dealContentMain {
  font-size: 28px;
}
.fixTitle {
  width: 1660px;
  text-align: left;
  left: 50%;
  margin-left: -830px;
}
</style>



