import Vue from 'vue'
import Vuex from 'vuex'
import KcmimDeaLindicate from './com/KcmimDeaLindicate.js'
import dailyMapSatus from './com/dailyMapSatus.js'
import IntegratedOperation from './com/IntegratedOperation.js'
Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    errorDeviceList: {},
    errorBorderList: {}
  },
  getters: {
    getErrorDeviceList: state => state.errorDeviceList,
    getErrorBorderList: state => state.errorBorderList
  },
  mutations: {
    setErrorDeviceList (state, data) {
      state.errorDeviceList = data
    },
    setErrorBorderList (state, data) {
      state.errorBorderList = data
    }
  },
  actions: {},
  modules: {
    IntegratedOperation: IntegratedOperation,
    dailyMapSatus: dailyMapSatus,
    KcmimDeaLindicate: KcmimDeaLindicate
  }
})



