<template>
  <section
    :style="{ height: this.contentHeight + 'px', overflow: this.overflow }"
  >
    <vue-seamless-scroll
      :data="listData"
      :class-option="optionHover"
      class="seamless-warp"
      :style="{ height: contentHeight + 'px' }"
    >
      <ul class="item">
        <li v-for="item in listData" :key="item.group" class="itemLi">
          <span
            class="itemTitle"
            v-for="(headerItem, index) in headerDate"
            :key="item.group + headerItem"
            :style="{
              width: index === 0 ? '255px' : '185px',
              flex: index === 0 ? '' : 'auto'
            }"
          >
            {{ getTheValue(headerItem, item) }}
          </span>
        </li>
      </ul>
    </vue-seamless-scroll>
  </section>
</template>
<script>
import tabelHeader from "@/assets/DailyOperation/shucai/v1_table1.png";
import tabelHeader1 from "@/assets/DailyOperation/shucai/v1_table2.png";
import tabelHeader2 from "@/assets/DailyOperation/shucai/v1_table3.png";
export default {
  data() {
    return {
      tabelHeader,
      tabelHeader1,
      tabelHeader2
    };
  },
  props: {
    sourDate: {
      required: false,
      default: () => [],
      type: Array
    },
    headerDate: {
      required: false,
      default: () => [],
      type: Array
    },
    contentHeight: {
      required: false,
      default: 500,
      type: Number
    },
    overflow: {
      required: false,
      default: "auto",
      type: String
    },
    runnring: {
      required: false,
      default: false,
      type: Boolean
    }
  },
  computed: {
    listData() {
      return this.sourDate.length > 0 ? this.sourDate : [];
    },
    optionHover() {
      return {
        singleHeight: 86,
        waitTime: 2500
      };
    }
  },
  methods: {
    getTheValue(indicate, value) {
      let back = "";
      switch (indicate) {
        case "市场名称":
          back = value.group;
          break;
        default:
          value.result &&
            value.result.forEach(element => {
              if (element.indicateName === indicate) {
                back = element.value;
              }
            });
          break;
      }
      return back;
    }
  }
};
</script>
<style scoped>
ul {
  margin: 0;
  padding: 0;
}
.seamless-warp {
  width: 100%;
  height: 500px;
  overflow: hidden;
}
.itemLi {
  display: flex;
  height: 86px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  align-items: center;
}
.itemLi span {
  width: 25%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.tabeleheader div {
  width: 50%;
  text-align: center;
}
.itemLi span:first-child {
  border-left: none;
  width: 280px;
}
</style>



