        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "保险业务数"
        },

        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "基金业务数1"
        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "证券业务数1"
        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "银行业务数1"
        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "期货业务数1"
        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "保险业务数1"
        },

        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "基金业务数2"
        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "证券业务数2"
        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "银行业务数2"
        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "期货业务数2"
        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "保险业务数2"
        },

        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "基金业务数3"
        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "证券业务数3"
        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "银行业务数3"
        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "期货业务数3"
        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "保险业务数3"
        },

        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "基金业务数4"
        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "证券业务数4"
        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "银行业务数4"
        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "期货业务数4"
        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "保险业务数4"
        },

        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "基金业务数5"
        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "证券业务数5"
        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "银行业务数5"
        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "期货业务数5"
        },
        {
          dateTime: tillTiem,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务统计",
          group: "保险业务数5"
        }
      ];
      this.getWebsocketMsg(test);
    },
    getWebsocketMsg(sourDate) {
      let self = this;
      if (!self.fristBack && sourDate.length > 0) {
        let toUpdatedCharts = false;
        for (let sourDateItem of sourDate) {
          if (sourDateItem.indicateName === "业务统计") {
            let target = self.charDate.findIndex(item => {
              return item.group === sourDateItem.group;
            });
            toUpdatedCharts = true;
            if (target !== -1) {
              self.$set(self.charDate, target, sourDateItem);
            } else {
              self.charDate.push(sourDateItem);
            }
          } else {
            let target = self.accountMsgList.findIndex(item => {
              return item.indicateName === sourDateItem.indicateName;
            });
            if (target !== -1) {
              self.$set(self.accountMsgList, target, sourDateItem);
            } else {
              self.accountMsgList.push(sourDateItem);
            }
          }
        }
        if (toUpdatedCharts) self.updatedCharts();
      }
      if (self.fristBack) {
        self.getCharsDate(sourDate);
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    },
    getCharsDate(sourDate) {
      let sour = sourDate || [];
      let allDate = sour;
      this.charDate = allDate.filter(item => {
        return item.indicateName === "业务统计";
      });
      this.accountMsgList = allDate.filter(item => {
        return item.indicateName !== "业务统计";
      });
      this.updatedCharts();
    },
    updatedCharts() {
      if (this.charDate.length > 0) {
        this.charDate.sort((a, b) => {
          return b.value - a.value;
        });
        try {
          let sour = JSON.parse(JSON.stringify(this.charDate));
          this.chartOb.changeData(sour);
        } catch (e) {
          this.createdChart();
        }
      } else {
        console.error("图表数据为空");
      }
    },
    createdChart() {
      try {
        this.chartOb.clear();
      } catch (e) {
        // console.log('尝试清空图表')
      }
      try {
        if (this.charDate.length > 0) {
          this.chartOb = new Chart({
            container: this.$refs.reference,
            padding: [180, 35, 120, 150],
            autoFit: true,
            height: 550
          });
          let sour = JSON.parse(JSON.stringify(this.charDate));
          let legendItem = [];
          sour.map(item => {
            if (!legendItem.includes(item.indicateName)) {
              legendItem.push(item.indicateName);
            }
          });
          this.chartOb.data(sour);

          this.chartOb.axis("group", {
            grid: null,
            label: {
              autoRotate: false,
              offset: 15,
              rotate: 0,
              style: {
                fontSize: 14,
                textAlign: "center",
                textBaseline: "top",
                fill: "#6ecaff",
                lineHeight: 16
              },
              formatter: value => {
                return value.split("").join("\n");
              }
            },
            line: {
              style: this.lineStyle
            }
          });

          this.chartOb.axis("value", {
            grid: null,
            label: {
              offset: 20,
              style: this.yStyle
            },
            line: {
              style: this.lineStyle
            }
          });
          this.chartOb.scale("value", {
            nice: true,
            min: 0,
            tickCount: 5
          });

          this.chartOb.tooltip({
            showCrosshairs: true,
            shared: true
          });

          let colors = [
            "l(270) 0:#5586ff 1:#37d7ff",
            "l(270) 0:#fe9844 1:#ffe535"
          ];
          let itemsList = legendItem.map((value, index) => {
            return {
              name: value, // 对应 itemName
              value: value, // 对应 itemValue
              marker: {
                symbol: "square", // marker 的形状
                style: {
                  r: 10, // marker 图形半径
                  fill: colors[index] // marker 颜色，使用默认颜色，同图形对应
                }
              } // marker 配置
            };
          });
          this.chartOb.legend({
            position: "top",
            offsetY: 100,
            items: itemsList,
            itemName: {
              style: {
                fontSize: 30,
                fill: "#fff"
              }
            }
          });

          this.chartOb.scale("group", {
            nice: false,
            tickCount: 50
          });
          this.chartOb.tooltip({
            showMarkers: false,
            shared: true
          });

          this.chartOb
            .interval()
            .position("group*value")
            .label("value", value => {
              return {
                style: {
                  fill: "#37d7ff",
                  fontSize: 18
                },
                animate: {
                  appear: {
                    animation: "label-appear",
                    delay: 0,
                    duration: 1000,
                    easing: "easeLinear"
                  },
                  update: {
                    animation: "label-update",
                    duration: 1000,
                    easing: "easeLinear"
                  }
                },
                offset: 15
              };
            })
            .animate({
              appear: {
                duration: 1000,
                easing: "easeLinear"
              },
              update: {
                duration: 1000,
                easing: "easeLinear"
              }
            })
            .color("indicateName", colors)
            .adjust([
              {
                type: "dodge",
                marginRatio: 0
              }
            ]);

          this.chartOb.interaction("active-region");
          this.chartOb.render();
          this.inited = true;
        } else {
          this.inited = false;
        }
        this.setTheInterval();
      } catch (e) {
        this.inited = false;
        console.error(JSON.stringify(e));
      }
    }
  },
  render(h) {
    return (
      <section
        class="mainBox"
        {...{
          style: { backgroundImage: " url(" + this.backgroundImage + ")" }
        }}
      >
        <section class="contentBox">
          <section class="accountBox">
            {this.accountMsgList.map(item => {
              return (
                <div class="accountMsg">
                  <span class="accountName">{item.group}:</span>
                  <span class="accountNum mainFont">{item.value}</span>
                </div>
              );
            })}
          </section>

          <section class="mainChartBox">
            <div class="title">当天业务办理数</div>
            <div id="{this.container}" class="container" ref="reference"></div>
          </section>
        </section>
      </section>
    );
  }
};
</script>

<style scoped>
.contentBox {
  position: absolute;
  z-index: 99;
  bottom: 0;
  left: 94px;
  width: 1705px;
  height: 758px;
}
.accountMsg {
  font-size: 40px;
  font-weight: bold;
  letter-spacing: 4px;
}
.accountName {
  font-weight: bold;
  color: #e0e8ff;
}
.accountNum {
  font-size: 72px;
}
.accountBox {
  display: flex;
  align-items: center;
  justify-content: center;
}
.accountMsg {
  width: 50%;
  padding: 40px;
}

.mainChartBox .title {
  width: 1660px;
  text-align: left;
  left: 50%;
  margin-left: -830px;
}
</style>



