<script>
import backgroundImage from "@/assets/image/background/3-1.jpg";
// import FlipClock from '@/components/FlipClock'
import polarAreaMap from "@/components/polarAreaMap";
import tuopan from "@/components/tuopan";
import cubeBox from "@/components/cubeBox3d";
// 定时器
// import setIntervalObject from '@/mixins/setIntervalObject'
import websockObject from "@/mixins/websock";

export default {
  name: "rightHeader",
  mixins: [websockObject],
  data() {
    return {
      backgroundImage,
      polarAreaMapDate: [],
      time: "",
      presentTest: 0,
      // eslint-disable-next-line no-undef
      orderList: righHeader,
      leftObject: {},
      centerObject: {},
      rightObject: {},
      tillTimeObject: null,
      tillTime: ""
    };
  },
  components: {
    // FlipClock,
    polarAreaMap,
    tuopan,
    cubeBox
  },
  beforeDestroy() {
    this.tillTimeObject && clearInterval(this.tillTimeObject);
  },
  mounted() {
    this.tillTimeObject && clearInterval(this.tillTimeObject);
    let _this = this;
    this.tillTimeObject = setInterval(() => {
      _this.tillTime = _this.Format("yyyy-MM-dd HH:mm:ss");
    }, 1000);
    // this.getCharsDate()
    // const test = [
    //   {'dateTime': 1599694958784, 'totalSize': 1100, 'type': 'top', 'value': [{'totalSize': 200, 'name': '路由器', 'valuePer': 30}, {'totalSize': 900, 'name': '防火墙', 'valuePer': 70}], 'indicateName': '网络设备类型', 'group': '网络设备类型'},
    //   {'dateTime': 1599695081206, 'totalSize': 1100, 'type': 'top', 'value': [{'totalSize': 200, 'name': 'oracle', 'valuePer': 30}, {'totalSize': 900, 'name': 'mysql', 'valuePer': 60}, {'totalSize': 100, 'name': 'sqlServer', 'valuePer': 10}], 'indicateName': '数据库类型', 'group': '数据库类型'}, {'dateTime': 1599695282006, 'totalSize': 1100, 'type': 'top', 'value': [{'totalSize': 200, 'name': '华为', 'valuePer': 30}, {'totalSize': 900, 'name': '思科', 'valuePer': 70}], 'indicateName': '网络设备厂商', 'group': '网络设备厂商'}, {'dateTime': 1599696068673, '深圳后海': {'totalSize': 200, 'name': '深圳后海', 'valuePer': 30}, 'totalSize': 9080, '东莞凤岗': {'totalSize': 900, 'name': '东莞凤岗', 'valuePer': 70}, '上海金桥': {'totalSize': 10, 'name': '上海金桥', 'valuePer': 9}, 'type': 'bottom', 'indicateName': '机房主机数量', 'group': '机房主机数量'}, {'dateTime': 1599709208083, '虚拟机': {'totalSize': 200, 'name': '虚拟机', 'valuePer': 30}, 'totalSize': 9080, '实体机': {'totalSize': 900, 'name': '实体机', 'valuePer': 70}, 'type': 'bottom', 'indicateName': '虚机实机', 'group': '虚机实机'}, {'dateTime': 1599709755116, 'totalSize': 9080, 'linux': {'totalSize': 900, 'name': 'linux', 'valuePer': 70}, 'type': 'bottom', 'windows': {'totalSize': 200, 'name': 'windows', 'valuePer': 30}, 'indicateName': '操作系统类型', 'unix': {'totalSize': 900, 'name': 'unix', 'valuePer': 70}, 'group': '操作系统类型'}]
    // this.getWebsocketMsg(test)

    // eslint-disable-next-line no-undef
    this.wsuri = BaseUrl.rightHeader.websockUrl;
    // this.useTheTestDate();
  },
  methods: {
    useTheTestDate() {
      let m = 100;
      let n = 0;
      let baes = [
        {
          dateTime: 1599694958784,
          totalSize: 1100,
          type: "top",
          value: [
            {
              totalSize: Math.floor(Math.random() * (m - n) + n),
              name: "山石路由器1",
              valuePer: Math.floor(Math.random() * (m - n) + n)
            },
            {
              totalSize: Math.floor(Math.random() * (m - n) + n),
              name: "山石路由器2",
              valuePer: Math.floor(Math.random() * (m - n) + n)
            },
            {
              totalSize: Math.floor(Math.random() * (m - n) + n),
              name: "山石路由器3",
              valuePer: Math.floor(Math.random() * (m - n) + n)
            },
            {
              totalSize: Math.floor(Math.random() * (m - n) + n),
              name: "山石路由器4",
              valuePer: Math.floor(Math.random() * (m - n) + n)
            },
            {
              totalSize: Math.floor(Math.random() * (m - n) + n),
              name: "山石路由器5",
              valuePer: Math.floor(Math.random() * (m - n) + n)
            },
            {
              totalSize: Math.floor(Math.random() * (m - n) + n),
              name: "山石路由器6",
              valuePer: Math.floor(Math.random() * (m - n) + n)
            },
            {
              totalSize: Math.floor(Math.random() * (m - n) + n),
              name: "山石路由器7",
              valuePer: Math.floor(Math.random() * (m - n) + n)
            },
            {
              totalSize: Math.floor(Math.random() * (m - n) + n),
              name: "山石路由器8",
              valuePer: Math.floor(Math.random() * (m - n) + n)
            },
            {
              totalSize: Math.floor(Math.random() * (m - n) + n),
              name: "路由器",
              valuePer: Math.floor(Math.random() * (m - n) + n)
            },
            {
              totalSize: Math.floor(Math.random() * (m - n) + n),
              name: "防火墙",
              valuePer: Math.floor(Math.random() * (m - n) + n)
            }
          ],
          indicateName: "网络设备类型",
          group: "网络设备类型"
        },
        {
          dateTime: 1599694958784,
          totalSize: 1100,
          type: "top",
          value: [
            {
              totalSize: Math.floor(Math.random() * (m - n) + n),
              name: "路由器",
              valuePer: Math.floor(Math.random() * (m - n) + n)
            },
            {
              totalSize: Math.floor(Math.random() * (m - n) + n),
              name: "防火墙",
              valuePer: Math.floor(Math.random() * (m - n) + n)
            }
          ],
          indicateName: "数据库类型",
          group: "数据库类型"
        },
        {
          dateTime: 1599694958784,
          totalSize: 1100,
          type: "top",
          value: [
            {
              totalSize: Math.floor(Math.random() * (m - n) + n),
              name: "路由器",
              valuePer: Math.floor(Math.random() * (m - n) + n)
            },
            {
              totalSize: Math.floor(Math.random() * (m - n) + n),
              name: "防火墙",
              valuePer: Math.floor(Math.random() * (m - n) + n)
            }
          ],
          indicateName: "网络设备厂商",
          group: "网络设备厂商"
        },
        {
          dateTime: 1599709208083,
          虚拟机: {
            totalSize: 200,
            name: "虚拟机",
            valuePer: Math.floor(Math.random() * (m - n) + n)
          },
          totalSize: 9080,
          实体机: {
            totalSize: 900,
            name: "实体机",
            valuePer: Math.floor(Math.random() * (m - n) + n)
          },
          type: "bottom",
          indicateName: "虚机实机",
          group: "虚机实机"
        }
      ];
      this.getWebsocketMsg(baes);
    },
    getWebsocketMsg(sourDate) {
      let self = this;
      if (sourDate.length > 0) {
        let polarAreaMapSour = [];
        sourDate.forEach(element => {
          if (self.orderList.chartsIndicate.includes(element.indicateName)) {
            polarAreaMapSour.push(element);
          }
          if (self.orderList.bottomLeft.indicateName === element.indicateName) {
            self.$set(self.$data, "leftObject", element);
          }
          if (
            self.orderList.bottomCenter.indicateName === element.indicateName
          ) {
            self.$set(self.$data, "centerObject", element);
          }
          if (
            self.orderList.bottomRight.indicateName === element.indicateName
          ) {
            self.$set(self.$data, "rightObject", element);
          }
        });
        if (polarAreaMapSour.length > 0) {
          let base = JSON.parse(JSON.stringify(self.polarAreaMapDate));
          polarAreaMapSour.forEach(sourItem => {
            let target = base.findIndex(item => {
              return sourItem.indicateName === item.indicateName;
            });
            if (target !== -1) {
              base[target] = sourItem;
            } else {
              base.push(sourItem);
            }
          });
          self.$set(self.$data, "polarAreaMapDate", base);
        }
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    },
    getCharsDate() {
      this.$axios({
        method: "get",
        url: "/api/typePre",
        data: {}
      })
        .then(response => {
          let sour = response.data.responseBody || [];
          this.$set(this.$data, "polarAreaMapDate", sour);
        })
        .catch(() => {
          console.error("请求失败!");
        });
    }
  },
  render(h) {
    return (
      <section
        class="mainBox"
        {...{
          style: { backgroundImage: " url(" + this.backgroundImage + ")" }
        }}
      >
        <section class="timeClock">{this.tillTime}</section>
        <section class="contentBox">
          <section class="mainChartBox">
            <div class="title">IT资产类型分析</div>
            <section class="polarAreaMapList" style="padding-top: 100px;">
              {this.polarAreaMapDate.map(polarAreaMapItem => {
                return (
                  <section class="polarAreaMapItem">
                    <div class="polarAreaMapItemName">
                      {polarAreaMapItem.group}
                    </div>
                    <polarAreaMap
                      ChartSour={polarAreaMapItem.value}
                    ></polarAreaMap>
                  </section>
                );
              })}
            </section>
          </section>
          <section class="mainListBox">
            <section class="polarAreaMapList">
              <div class="polarAreaMapItem">
                <div class="polarAreaMapItemName">各机房主机数量占比</div>
                <section class="polarAreaMapList" style="height:200px;">
                  {this.orderList.bottomLeft.showIndicateName.map(
                    polarAreaMapItem => {
                      return (
                        <tuopan
                          title={polarAreaMapItem}
                          present={
                            this.leftObject[polarAreaMapItem]
                              ? this.leftObject[polarAreaMapItem].valuePer
                              : 0
                          }
                        ></tuopan>
                      );
                    }
                  )}
                </section>
              </div>
              <div class="polarAreaMapItem">
                <div class="polarAreaMapItemName">虚机实机占比</div>
                <section class="polarAreaMapList" style="height:200px;">
                  {this.orderList.bottomCenter.showIndicateName.map(
                    polarAreaMapItem => {
                      return (
                        <cubeBox
                          style="width:50%;"
                          title={polarAreaMapItem}
                          present={
                            this.centerObject[polarAreaMapItem]
                              ? this.centerObject[polarAreaMapItem].valuePer
                              : 0
                          }
                        ></cubeBox>
                      );
                    }
                  )}
                </section>
              </div>
              <div class="polarAreaMapItem">
                <div class="polarAreaMapItemName">操作系统类型占比</div>
                <section class="polarAreaMapList" style="height:200px;">
                  {this.orderList.bottomRight.showIndicateName.map(
                    polarAreaMapItem => {
                      return (
                        <tuopan
                          title={polarAreaMapItem}
                          present={
                            this.rightObject[polarAreaMapItem]
                              ? this.rightObject[polarAreaMapItem].valuePer
                              : 0
                          }
                        ></tuopan>
                      );
                    }
                  )}
                </section>
              </div>
            </section>
          </section>
        </section>
      </section>
    );
  }
};
</script>

<style scoped>
.timeClock {
  position: absolute;
  right: 65px;
  top: 165px;
  font-size: 78px;
  color: #e0e8ff;
  font-weight: bold;
}
.contentBox {
  position: absolute;
  z-index: 99;
  bottom: 0;
  left: 75px;
  width: 1772px;
  height: 758px;
  display: flex;
  flex-flow: column;
}
.mainChartBox {
  height: 480px;
  overflow: hidden;
}
.polarAreaMapList {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.polarAreaMapItem {
  width: 33.3%;
  min-height: 345px;
}
.mainChartBox .title {
  left: 0;
}
.polarAreaMapItem .polarAreaMapItemName {
  font-size: 32px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 96px;
  letter-spacing: 3px;
  color: #e0e8ff;
  text-align: left;
  padding-left: 0px;
}
.mainListBox {
  flex: auto;
  overflow: hidden;
}
</style>



