export default {
  state: {
    routerStatus: {}
  },
  mutations: {
    setRouterStatus (state, data) {
      state.routerStatus = data
    }
  },
  getters: {
    setRouterStatus: state => state.routerStatus
  },
  actions: {
    setTheRouterStatus: ({ commit }, data) => {
      return commit('setRouterStatus', data)
    }
  }
}



