<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: "App",
  mounted() {
    console.log("update at :" + "1123 1100");
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #e0e8ff;
}
.mainTitile {
  font-weight: 400;
  color: #e0e8ff;
}
body {
  width: 1920px;
  height: 1080px;
  overflow-y: scroll;
  overflow-x: hidden;
  background: #090c28;
}
.fullBox {
  transform: scale(0.3333, 0.3333);
  transform-origin: 0 0 0;
}
body,
p {
  padding: 0;
  margin: 0;
}
.mainFont {
  background-image: -webkit-linear-gradient(bottom, #f5b517, #f5c31c, #fff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.indicatenName {
  color: #6ecaff;
}
.mainChartBox {
  position: relative;
  min-height: 60px;
}
.mainChartBox .title {
  position: absolute;
  font-size: 44px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 96px;
  letter-spacing: 0px;
  color: #e0e8ff;
  left: 40px;
  top: 15px;
}
.mainBox {
  width: 1920px;
  height: 1080px;
  position: relative;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
/* 设置滚动条的样式 */
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
/* 滚动槽 */
::-webkit-scrollbar-track {
  background: #090c28;
  border-radius: 10px;
}
/* 滚动条滑块 */
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #a3e0ff;
}
::-webkit-scrollbar-thumb:window-inactive {
  background: #a3e0ff;
}
ul {
  margin: 0;
  padding: 0;
}
</style>



