<template>
  <section
    class="mainBox"
    :style="{ backgroundImage: ' url(' + backgroundImage + ')' }"
  >
    <section class="contentBox">
      <section class="mainChartBox">
        <div class="title">外接机构交易量</div>
        <div :id="container" class="container" ref="reference"></div>
      </section>
    </section>
    <section class="contentBox" style="height:120px;">
      <section class="mainChartBox">
        <div class="title">系统概览</div>
      </section>
    </section>
  </section>
</template>

<script>
import backgroundImage from "@/assets/image/background/3-2.jpg";

// 定时器
// import setIntervalObject from '@/mixins/setIntervalObject'

import websockObject from "@/mixins/websock";

// 图表基础引用
import ChartBase from "@/mixins/ChartBase";
import { Chart } from "@antv/g2";
export default {
  name: "rightCenter",
  mixins: [ChartBase, websockObject],
  data() {
    return {
      backgroundImage,
      newestMap: new Map(),
      timeListMap: new Map(),
      legendLength: 1,
      timeLimit: 0,
      unit: "",
      tillTimeObject: null
    };
  },
  beforeDestroy() {
    this.tillTimeObject && clearInterval(this.tillTimeObject);
  },
  mounted() {
    let _this = this;
    this.initTheList().then(() => {
      // eslint-disable-next-line no-undef
      _this.wsuri = BaseUrl.rightCenter.websockUrl;
      // _this.useTheTestDate()
    });
    // _this.useTheTestDate()
    this.tillTimeObject && clearInterval(this.tillTimeObject);
    this.tillTimeObject = setInterval(() => {
      let historyTime = window.localStorage.getItem("clearTime")
        ? window.localStorage.getItem("clearTime")
        : 0;
      let tillNow = new Date().getTime();
      if (tillNow > historyTime) {
        _this.initTheList();
      }
    }, 60 * 60 * 1000);
    // eslint-disable-next-line no-undef
    // this.wsuri = BaseUrl.rightCenter.websockUrl
  },
  methods: {
    async initTheList() {
      let timeList = await this.getTheTimeBase();
      let allList = [];
      this.timeListMap = new Map();
      timeList &&
        timeList.map(item => {
          let baseItem = [
            {
              dateTime: item,
              type: "all",
              value: 0,
              indicateName: "恒生pb",
              group: "恒生pb"
            },
            {
              dateTime: item,
              type: "all",
              value: 0,
              indicateName: "迅投pb",
              group: "迅投pb"
            },
            {
              dateTime: item,
              type: "all",
              value: 0,
              indicateName: "络町pb",
              group: "络町pb"
            },
            {
              dateTime: item,
              type: "all",
              value: 0,
              indicateName: "量化家pb",
              group: "量化家pb"
            }
          ];
          allList = [...allList, ...baseItem];
          let keys = this.dateFormatAll(new Date(item));
          this.timeListMap.set(keys, allList.length);
        });
      this.charDate = allList;
      if (this.chartOb === null) {
        this.createdChart();
      } else {
        this.updatedCharts();
      }
      return new Promise((resolve, reject) => {
        resolve();
      });
    },
    useTheTestDate() {
      let m = 100;
      let n = 0;
      let till = new Date("2020-11-19 09:00:00").getTime();
      const test = [
        {
          dateTime: till + this.timeLimit * 60 * 1000,
          type: "all",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "恒生pb",
          group: "恒生pb"
        },
        {
          dateTime: till + this.timeLimit * 60 * 1000,
          type: "all",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "迅投pb",
          group: "迅投pb"
        },
        {
          dateTime: till + this.timeLimit * 60 * 1000,
          type: "all",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "络町pb",
          group: "络町pb"
        },
        {
          dateTime: till + this.timeLimit * 60 * 1000,
          type: "all",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "量化家pb",
          group: "量化家pb"
        }
      ];
      this.timeLimit = this.timeLimit + 1;
      this.getWebsocketMsg(test);
      let _this = this;
      setTimeout(() => {
        _this.useTheTestDate();
      }, 5000);
    },
    getWebsocketMsg(sourDate) {
      let self = this;
      if (sourDate.length > 0) {
        self.getCharsDate(sourDate);
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    },
    async getCharsDate(sourDate) {
      let historyTime = window.localStorage.getItem("clearTime")
        ? window.localStorage.getItem("clearTime")
        : 0;
      let tillNow = new Date().getTime();
      if (tillNow > historyTime) {
        window.localStorage.removeItem("jiaoyiTimeBase");
        await this.initTheList();
      }
      if (sourDate.length > 0) {
        sourDate.map(item => {
          let keys = this.dateFormatAll(new Date(item.dateTime));
          if (this.timeListMap.has(keys)) {
            let target = this.timeListMap.get(keys);
            for (let i = target - 4; i < target; i++) {
              if (this.charDate[i].indicateName === item.indicateName) {
                this.newestMap.set(item.indicateName, item.value);
                this.charDate[i].value = item.value;
                break;
              }
            }
          }
        });
        this.updatedCharts();
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
      // let sour = sourDate || []
      // let allDate = sour
      // this.charDate = [...this.charDate, ...allDate].slice(-10 * this.legendLength)
      // let maxNum = 0
      // this.charDate.forEach(element => {
      //   if (element.indicateName) {
      //     maxNum = maxNum > element.value ? maxNum : element.value
      //     this.newestMap.set(element.indicateName, element.value)
      //   }
      // })
      // if (maxNum > 999999) {
      //   this.unit = '万元'
      // } else {
      //   this.unit = ''
      // }
      // this.legendLength = this.newestMap.size
      // this.updatedCharts()
    },
    getTheNewValue(name) {
      let back = this.newestMap.get(name) / 10000;
      return (
        ":" +
        (this.newestMap.has(name)
          ? this.unit === ""
            ? this.newestMap.get(name)
            : (back < 1 ? back : parseInt(back)) + this.unit
          : 0)
      );
    },
    createdChart() {
      try {
        this.chartOb.clear();
      } catch (e) {
        // console.log('尝试清空图表')
      }
      try {
        if (this.charDate.length > 0) {
          this.chartOb = new Chart({
            container: this.$refs.reference,
            padding: [280, 35, 45, 200],
            autoFit: true,
            height: 940
          });
          let sour = JSON.parse(JSON.stringify(this.charDate));
          let legendItem = [];
          let newsMap = new Map();
          sour.map(item => {
            newsMap.set(item.indicateName, item.value);
            if (!legendItem.includes(item.indicateName)) {
              legendItem.push(item.indicateName);
            }
          });
          this.legendLength = legendItem.length;
          this.chartOb.data(sour);
          this.chartOb.axis("value", {
            grid: null,
            label: {
              offset: 20,
              style: this.yStyle
            },
            line: {
              style: this.lineStyle
            }
          });
          this.chartOb.axis("dateTime", {
            grid: null,
            label: {
              autoRotate: false,
              offset: 25,
              rotate: 0,
              style: this.xStyle,
              formatter: value => {
                return value;
              }
            },
            line: {
              style: this.lineStyle
            }
          });
          this.chartOb.scale("dateTime", {
            type: "time",
            mask: "HH:mm",
            tickInterval: 60 * 60 * 1000
          });
          this.chartOb.scale("value", {
            min: 0,
            tickCount: 5
          });
          this.chartOb.tooltip({
            showCrosshairs: true,
            shared: true
          });
          let colors = ["#feb33f", "#1fd1b8", "#3ab9fe", "#ff536e"];
          let itemsList = legendItem.map((value, index) => {
            return {
              name: value, // 对应 itemName
              value: value, // 对应 itemValue
              marker: {
                symbol: "square", // marker 的形状
                style: {
                  r: 10, // marker 图形半径
                  fill: colors[index % colors.length] // marker 颜色，使用默认颜色，同图形对应
                }
              }
            };
          });
          this.chartOb.legend("indicateName", {
            position: "top",
            offsetY: 180,
            itemWidth: 400,
            layout: "horizontal",
            items: itemsList,
            itemName: {
              style: {
                fontSize: 30,
                fill: "#6ecaff"
              }
            },
            itemValue: {
              formatter: (text, item, index) => this.getTheNewValue(text), // 格式化文本函数
              style: {
                fontSize: 30,
                fontWeight: 700,
                fill: "l(90) 0:#e0e8ff 0.5:#7ec2f3 1:#1890ff"
              }
            }
          });
          this.chartOb
            .line()
            .size(4)
            .position("dateTime*value")
            .color("indicateName", colors)
            .shape("smooth");

          this.chartOb.interaction("active-region");
          this.chartOb.render();
        } else {
        }
        this.setTheInterval();
      } catch (e) {
        this.inited = false;
        console.error(JSON.stringify(e));
      }
    }
  }
};
</script>

<style scoped>
.mainChartBox .title {
  left: 0;
}
.contentBox {
  position: absolute;
  height: 100%;
  width: 1772px;
  z-index: 99;
  bottom: 0;
  left: 75px;
  overflow: hidden;
}
.mainChartBox {
  height: 100%;
}
</style>



