
/**
 * 模块说明
 * @module 图表基础属性，基于G2
 *
 * @method updatedCharts，更新图表
 * @param 空
 * @return 空
 *
 * @method createdChart，创建图表
 * @param 空
 * @return 空
 *
 */
const ChartBase = {
  data () {
    return {
      charDate: [],
      chartOb: null,
      lineStyle: {
        fill: '#3bc1ff',
        fillOpacity: 0.7,
        stroke: '#3bc1ff',
        lineWidth: 3,
        strokeOpacity: 0.7,
        endArrow: true
      },
      xStyle: {
        fontSize: 30,
        textAlign: 'center',
        textBaseline: 'middle',
        fill: '#6ecaff'
      },
      yStyle: {
        fontSize: 30,
        fill: '#6ecaff'
      },
      container: Math.random()
        .toString(36)
        .substr(2)
    }
  },
  methods: {
    updatedCharts () {
      if (this.charDate.length > 0) {
        try {
          let sour = JSON.parse(JSON.stringify(this.charDate))
          if (this.chartOb) {
            sour.sort((a, b) => {
              return a.dateTime - b.dateTime
            })
            this.chartOb.changeData(sour)
          } else {
            this.createdChart()
          }
        } catch (e) {
          this.createdChart()
        }
      } else {
        console.error('图表数据为空')
      }
    },
    createdChart () {}
  }
}

export default ChartBase



