// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import echarts from 'echarts'
import axios from 'axios'
import scroll from 'vue-seamless-scroll'
import store from './store'
import animated from 'animate.css'
Vue.use(animated)
Vue.use(scroll)

Vue.prototype.$echarts = echarts
Vue.prototype.$axios = axios
require('./mock.js')
Vue.config.productionTip = false
Vue.prototype.dateFormatAll = function (date) {
  let Minutes = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes() + ''
  let Hours = date.getHours() < 10 ? '0' + date.getHours() : date.getHours() + ''
  let back = Hours + ':' + Minutes
  return back
}
Vue.prototype.Format = function (fmt) {
  let tillNow = new Date()
  var o = {
    'M+': tillNow.getMonth() + 1, // 月份
    'd+': tillNow.getDate(), // 日
    'H+': tillNow.getHours(), // 小时
    'm+': tillNow.getMinutes(), // 分
    's+': tillNow.getSeconds(), // 秒
    'q+': Math.floor((tillNow.getMonth() + 3) / 3), // 季度
    'S': tillNow.getMilliseconds() // 毫秒
  }
  if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (tillNow.getFullYear() + '').substr(4 - RegExp.$1.length))
  for (var k in o) { if (new RegExp('(' + k + ')').test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length))) }
  return fmt
}
Vue.prototype.getTheTimeBase = function () {
  let historyTime = window.localStorage.getItem('clearTime') ? window.localStorage.getItem('clearTime') : 0
  let tillNow = new Date().getTime()
  if (tillNow > historyTime) {
    window.localStorage.removeItem('jiaoyiTimeBase')
  }
  return new Promise((resolve, reject) => {
    let jiaoyiTimeBase = []
    let tillnow = this.Format('yyyy-MM-dd')
    if (window.localStorage.getItem('jiaoyiTimeBase')) {
      jiaoyiTimeBase = window.localStorage.getItem('jiaoyiTimeBase').split(',')
      jiaoyiTimeBase = jiaoyiTimeBase.map(item => {
        return Number(item)
      })
      resolve(jiaoyiTimeBase)
    } else {
      let startTimeAll = new Date(tillnow + ' 09:00:00').getTime()
      let endTimeAll = new Date(tillnow + ' 15:00:00').getTime()
      let minTime = 60 * 1000
      do {
        let theItemTime = startTimeAll
        jiaoyiTimeBase.push(theItemTime)
        startTimeAll = startTimeAll + minTime
      } while (startTimeAll <= endTimeAll)
      window.localStorage.setItem('jiaoyiTimeBase', jiaoyiTimeBase)
      let clearTime = new Date(tillnow + ' 23:59:59').getTime()
      window.localStorage.setItem('clearTime', clearTime)
      resolve(jiaoyiTimeBase)
    }
  })
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



