export default {
  state: {
    top: [],
    middle: [],
    bottom: []
  },
  mutations: {
    initShowList (state) {
      state.top = []
      state.middle = []
      state.bottom = []
    },
    setIntegratedOperation (state, data) {
      state.top = data['top']
      state.middle = data['middle']
      state.bottom = data['bottom']
    },
    setIntegratedOperationHeader (state, data) {
      state.top = data
    },
    setIntegratedOperationBottom (state, data) {
      state.bottom = data
    }
  },
  getters: {
    IntegratedOperationTop: state => state.top,
    IntegratedOperationMiddle: state => state.middle,
    IntegratedOperationBottom: state => state.bottom
  },
  actions: {
    initShowList: ({ commit }) => {
      return commit('initShowList')
    },
    setIntegratedOperation: ({ commit }, data) => {
      return commit('setIntegratedOperation', data)
    },
    setIntegratedOperationHeader: ({ commit }, data) => {
      return commit('setIntegratedOperationHeader', data)
    },
    setIntegratedOperationBottom: ({ commit }, data) => {
      return commit('setIntegratedOperationBottom', data)
    }
  }
}



