<template>
  <section
    class="mainBox"
    :style="{ backgroundImage: ' url(' + backgroundImage + ')' }"
  >
    <section class="contentBox">
      <section class="mainChartBox">
        <div class="title">外围系统</div>
      </section>
      <section class="indicateList">
        <div
          class="indicateBox"
          v-for="indicate in indicateList"
          :key="indicate.id"
        >
          <div class="indicateValue mainFont">
            {{ indicate.value }}
          </div>
          <div class="indicatenName">
            {{ indicate.indicateName }}
          </div>
        </div>
      </section>
    </section>
  </section>
</template>

<script>
import backgroundImage from "@/assets/image/background/1-3.jpg";
// 定时器
// import setIntervalObject from '@/mixins/setIntervalObject'
import websockObject from "@/mixins/websock";
export default {
  name: "leftBottom",
  mixins: [websockObject],
  data() {
    return {
      backgroundImage,
      // eslint-disable-next-line no-undef
      orderList: ObjectOrder.leftBottom,
      indicateList: []
    };
  },
  mounted() {
    // eslint-disable-next-line no-undef
    this.wsuri = BaseUrl.leftBottom.websockUrl;
    // this.useTheTestDate()
  },
  methods: {
    useTheTestDate() {
      let test = [
        {
          dateTime: 1599643784043,
          value: 841,
          indicateName: "外围系统在线人数"
        },
        {
          dateTime: 1599643784043,
          value: 573,
          indicateName: "外围系统登陆人数"
        }
      ];
      this.getWebsocketMsg(test);
    },
    getWebsocketMsg(sourDate) {
      if (!this.fristBack && sourDate.length > 0) {
        for (let sourDateItem of sourDate) {
          let target = this.orderList.findIndex(item => {
            return item === sourDateItem.indicateName;
          });
          if (target !== -1) {
            this.$set(this.indicateList, target, sourDateItem);
          }
        }
      }
      if (this.fristBack) {
        this.getCharsDate(sourDate);
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    },
    getCharsDate(sourDate) {
      let sour = sourDate || [];
      if (sour.length > 0) {
        this.indicateList = sour.sort(
          (a, b) =>
            this.orderList.indexOf(a.indicateName) -
            this.orderList.indexOf(a.indicateName)
        );
      }
    }
  }
};
</script>

<style scoped>
.contentBox {
  position: absolute;
  top: 0;
  left: 94px;
  width: 1705px;
  height: 1080px;
}
.mainChartBox {
  width: 100%;
  height: 100%;
}
.indicateList {
  position: absolute;
  top: 190px;
  right: 195px;
  width: 470px;
}
.indicateBox {
  height: 160px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
}
.indicateValue {
  font-size: 60px;
  font-weight: bold;
  line-height: 1;
}
.indicatenName {
  font-size: 36px;
}
.mainChartBox .title {
  width: 1660px;
  text-align: left;
  left: 50%;
  margin-left: -830px;
}
</style>



