<template>
  <section class="mapPoint" :id="container"></section>
</template>

<script>
import { mapGetters } from "vuex";
// import echarts from 'echarts'
import jifang from "@/assets/image/shucai/v2_jifang.png";
import lightPoint from "@/assets/image/shucai/lightPoint.png";
// import {province, jifangPointDate} from './point'
// import setIntervalObject from '@/mixins/setIntervalObject'
export default {
  name: "mapPoint",
  // mixins: [setIntervalObject],
  data() {
    return {
      jifang,
      lightPoint,
      jifangPointDate: [],
      historyDate_Point: [],
      historyDate_Line: [],
      sourDate: null,
      myChart: null,
      container: Math.random()
        .toString(36)
        .substr(2)
    };
  },
  mounted() {
    this.myChart = this.$echarts.init(document.getElementById(this.container));
    this.loadSpeed = 10000;
    // this.getCharsDate()
  },
  computed: {
    ...mapGetters(["IntegratedOperationMiddle"])
  },
  watch: {
    IntegratedOperationMiddle: {
      handler(newName, oldName) {
        if (newName !== undefined && newName.length > 0) {
          let _this = this;
          this.$nextTick(() => {
            _this.getCharsDate(newName);
          });
        }
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    updateLine(sourDate, refreshJiFang = true) {
      if (this.sourDate === null) {
        this.sourDate = [];
      }
      if (
        sourDate.from === undefined ||
        sourDate.to === undefined ||
        this.sourDate === null
      ) {
        return false;
      }
      let base = JSON.parse(JSON.stringify(this.sourDate));
      let isAddLine = true;
      for (let keys in base) {
        let item = base[keys];
        if (item && item.from !== undefined && item.to !== undefined) {
          if (
            item.from.roomName === sourDate.from.roomName &&
            item.to.address === sourDate.to.address
          ) {
            this.sourDate[keys] = sourDate;
            isAddLine = false;
            break;
          }
        }
      }
      if (isAddLine) {
        this.sourDate.push(sourDate);
      }
      this.initChart(refreshJiFang);
    },
    getCharsDate(sourDate) {
      let sour = sourDate || {};
      this.sourDate = sour;
      this.initChart();
    },
    initChart(refreshJiFang = true) {
      /*
       * 需要参数
       */
      let sourDateBase = JSON.parse(JSON.stringify(this.sourDate));
      let jifangPointDateBase = JSON.parse(
        JSON.stringify(this.jifangPointDate)
      );

      let jifangPoint = [];
      let lins = [];
      let showLine = [];
      let nodesLine = [];
      let nameOnly = [];
      if (sourDateBase && sourDateBase.length > 0) {
        sourDateBase.forEach((element, index) => {
          if (
            element &&
            element.from !== undefined &&
            element.to !== undefined
          ) {
            jifangPointDateBase[element.from.roomName] = [
              element.from.x,
              element.from.y
            ];
            let nodeName = element.to.name;
            if (nameOnly.includes(element.to.name)) {
              nodeName = nodeName + index;
            } else {
              nameOnly.push(nodeName);
            }
            nodesLine.push({
              name: nodeName,
              draggable: false,
              value: [element.to.x, element.to.y],
              itemStyle: {
                color: "#f6ff54"
              }
            });
            let theLineColor = element.value === 1 ? "red" : "#f6ff54";
            let showLineItem = {
              type: "lines",
              zlevel: 1,
              coordinateSystem: "cartesian2d",
              effect: {
                show: element.value === 0,
                period: 4, // 箭头指向速度，值越小速度越快
                trailLength: 0, // 特效尾迹长度[0,1]值越大，尾迹越长重
                symbol: "image://" + this.lightPoint, // 箭头图标
                symbolSize: 120 // 图标大小
              },
              // effect: false,
              lineStyle: {
                normal: {
                  curveness: element.from.x > element.to.x ? -0.2 : 0.1,
                  type: "solid",
                  opacity: 1,
                  color: theLineColor,
                  width: 8
                }
              },
              data: [
                {
                  coords: [
                    [element.from.x, element.from.y + 5],
                    [element.to.x, element.to.y]
                  ]
                }
              ]
            };
            showLine.push(showLineItem);
          } else {
            console.error(element);
          }
        });
      }

      if (refreshJiFang) {
        if (jifangPointDateBase) {
          for (let jifangKey in jifangPointDateBase) {
            let jifangPointItem = {
              name: jifangKey,
              symbol: "image://" + this.jifang,
              symbolSize: 200,
              draggable: false,
              fixed: true,
              // focusNodeAdjacency: true,
              value: jifangPointDateBase[jifangKey],
              label: {
                // 文字2
                normal: {
                  position: "bottom",
                  show: false,
                  distance: 5,
                  textStyle: {
                    fontSize: 40,
                    color: "#fff",
                    fontWeight: "500"
                  }
                }
              }
            };
            jifangPoint.push(jifangPointItem);
          }
        }
        if (jifangPoint && jifangPoint.length > 0) {
          for (let size = 0; size < jifangPoint.length; size++) {
            for (let num = 1 + size; num < jifangPoint.length; num++) {
              let jifangLine = {
                type: "lines",
                coordinateSystem: "cartesian2d",
                effect: {
                  show: true,
                  smooth: true,
                  trailLength: 0,
                  color: "rgb(112 230 255)",
                  symbol: "",
                  symbolSize: 0,
                  period: 10,
                  loop: true
                },
                lineStyle: {
                  normal: {
                    curveness: 0,
                    type: "solid",
                    opacity: 1,
                    color: "rgb(112 230 255)",
                    width: 10
                  }
                },
                data: [
                  {
                    coords: [
                      [
                        jifangPoint[size].value[0],
                        jifangPoint[size].value[1] - 5
                      ],
                      [jifangPoint[num].value[0], jifangPoint[num].value[1] - 5]
                    ]
                  }
                ]
              };
              lins.push(jifangLine);
            }
          }
        }
        for (let nodeKey of nodesLine) {
          let jifangPointItem = {
            name: nodeKey.name,
            symbol: "circle",
            symbolSize: 15,
            draggable: false,
            fixed: true,
            itemStyle: {
              normal: {
                color: "#f6ff54"
              }
            },
            // focusNodeAdjacency: true,
            value: nodeKey.value,
            label: {
              // 文字2
              normal: {
                position: "bottom",
                show: false,
                distance: 5,
                textStyle: {
                  fontSize: 40,
                  color: "#fff",
                  fontWeight: "500"
                }
              }
            }
          };
          jifangPoint.push(jifangPointItem);
        }
        this.historyDate_Point = JSON.parse(JSON.stringify(jifangPoint));
        this.historyDate_Line = JSON.parse(JSON.stringify(lins));
      } else {
        jifangPoint = this.historyDate_Point;
        lins = this.historyDate_Line;
      }

      let graphs = [
        {
          type: "graph",
          coordinateSystem: "cartesian2d",
          legendHoverLink: false,
          hoverAnimation: true,
          nodeScaleRatio: false,
          roam: false,
          lineStyle: {
            // 去掉自带的那条线，不要删
            normal: {
              width: 0,
              shadowColor: "none",
              color: "transparent"
            }
          },
          symbolSize: 0,
          data: jifangPoint
        }
      ];

      let marksData = [
        {
          name: "1",
          value: [0, 0]
        },
        {
          name: "2",
          value: [100, 0]
        },
        {
          name: "3",
          value: [100, 100]
        },
        {
          name: "4",
          value: [0, 100]
        },
        {
          name: "点1",
          value: [50, 50]
        }
      ];

      let option = {
        tooltip: {
          formatter: "{b}",
          textStyle: {
            fontSize: 50
          }
        },
        grid: {
          left: 50,
          right: 50,
          bottom: "4%",
          top: "6%",
          containLabel: true
        },
        xAxis: {
          scale: true,
          axisLine: false,
          axisLabel: false,
          splitLine: false
        },
        yAxis: {
          scale: true,
          axisLine: false,
          axisLabel: false,
          splitLine: false
        },
        series: [
          {
            type: "scatter",
            data: marksData,
            itemStyle: false,
            symbolSize: 0,
            draggable: false,
            fixed: true,
            label: {
              normal: {
                show: false
              }
            },
            tooltip: {
              show: false
            }
          },
          ...graphs,
          ...lins,
          ...showLine
        ]
      };
      this.myChart.setOption(option);
    }
  }
};
</script>
<style scoped>
.mapPoint {
  position: absolute;
  width: 3394px;
  height: 1978px;
  top: 600px;
  left: 1200px;
}
</style>



