<template>
  <section class="tuopan">
    <div :id="container" class="main"></div>
    <div class="title">{{title}}</div>
  </section>
</template>

<script>
import echarts from 'echarts'
export default {
  name: 'tuopan',
  data () {
    return {
      container: Math.random()
        .toString(36)
        .substr(2),
      myChart: {}
    }
  },
  props: {
    title: {
      required: false,
      default: '后海机房',
      type: String
    },
    present: {
      required: false,
      default: 0,
      type: Number
    }
  },
  watch: {
    present: {
      handler (newName, oldName) {
        let _this = this
        this.$nextTick(() => {
          _this.initChart()
        })
      },
      immediate: true,
      deep: true
    }
  },
  mounted () {
    this.myChart = this.$echarts.init(
      document.getElementById(this.container + '')
    )
    this.initChart()
  },
  methods: {
    initChart () {
      let dataArr = this.present
      let option = {
        backgroundColor: 'transparent',
        tooltip: {
          formatter: '{a} <br/>{b} : {c}%'
        },
        series: [
          {
            name: '最外部进度条',
            type: 'gauge',
            radius: '80%',
            splitNumber: 100,
            axisLine: {
              lineStyle: {
                color: [
                  [
                    dataArr / 100,
                    new echarts.graphic.LinearGradient(0, 1, 0, 0, [
                      {
                        offset: 0,
                        color: 'rgba(145,207,255,0)'
                      },
                      {
                        offset: 0.5,
                        color: 'rgba(145,207,255,0.2)'
                      },
                      {
                        offset: 1,
                        color: 'rgba(145,207,255,1)'
                      }
                    ])
                  ],
                  [1, 'rgba(28,128,245,.0)']
                ],
                width: 3
              }
            },
            axisLabel: {
              show: false
            },
            axisTick: {
              show: false
            },
            splitLine: {
              show: false
            },
            itemStyle: {
              show: false
            },
            detail: {
              show: false
            },
            title: {
              // 标题
              show: false
            },
            data: [
              {
                name: 'title',
                value: dataArr
              }
            ],
            pointer: {
              show: false
            },
            animationDuration: 4000
          },
          {
            name: '内部阴影',
            type: 'gauge',
            // center: ['20%', '50%'],
            radius: '70%',
            z: 4,
            splitNumber: 100,
            axisLine: {
              lineStyle: {
                color: [
                  [
                    dataArr / 100,
                    new echarts.graphic.LinearGradient(0, 1, 0, 0, [
                      {
                        offset: 0,
                        color: 'rgba(145,207,255,0)'
                      },
                      {
                        offset: 0.5,
                        color: 'rgba(145,207,255,0.1)'
                      },
                      {
                        offset: 1,
                        color: 'rgba(145,207,255,0.3)'
                      }
                    ])
                  ],
                  [1, 'rgba(28,128,245,.0)']
                ],
                width: 80
              }
            },
            axisLabel: {
              show: false
            },
            axisTick: {
              show: false
            },
            splitLine: {
              show: false
            },
            itemStyle: {
              show: false
            },
            detail: {
              formatter: function (value) {
                if (value !== 0) {
                  return parseInt(value) + '%'
                } else {
                  return 0
                }
              },
              offsetCenter: [0, 0],
              textStyle: {
                padding: [0, 0, 0, 0],
                fontSize: 25,
                color: '#f6e183'
              }
            },
            title: {
              // 标题
              show: false
            },
            data: [
              {
                name: 'title',
                value: dataArr
              }
            ],
            pointer: {
              show: false
            }
          },
          {
            name: '内部圈',
            type: 'gauge',
            z: 2,
            min: 0,
            max: 100,
            splitNumber: 100,
            radius: '70%',
            axisLine: {
              lineStyle: {
                color: [[1, '#e0e8ff']],
                width: 3,
                shadowColor: '#e0e8ff',
                shadowBlur: 6,
                shadowOffsetX: 0
              }
            },
            tooltip: {
              show: false
            },
            axisLabel: {
              show: false
            },
            axisTick: {
              show: false
            },
            splitLine: {
              show: false
            },
            itemStyle: {
              show: false
            },
            detail: {
              show: false
            },
            title: {
              // 标题
              show: false
            },
            data: [
              {
                name: 'title',
                value: dataArr
              }
            ],
            pointer: {
              show: true,
              length: '80%',
              radius: '20%',
              width: 3 // 指针粗细
            },
            animationDuration: 4000
          },
          {
            name: '内部刻度',
            type: 'gauge',
            radius: '65%',
            min: 0, // 最小刻度
            max: 100, // 最大刻度
            splitNumber: 100, // 刻度数量
            startAngle: 225,
            endAngle: -45,
            axisLine: {
              show: false,
              lineStyle: {
                width: 5,
                color: [[1, '#e0e8ff']]
              }
            }, // 仪表盘轴线
            axisLabel: {
              show: true,
              color: '#e0e8ff',
              distance: 2,
              fontSize: 16,
              formatter: function (v) {
                switch (v + '') {
                  case '0':
                    return '0'
                  // case '10':
                  //   return '10'
                  case '20':
                    return '20'
                  // case '30':
                  //   return '30'
                  case '40':
                    return '40'
                  // case '50':
                  //   return '50'
                  case '60':
                    return '60'
                  // case '70':
                  //   return '70'
                  case '80':
                    return '80'
                  // case '90':
                  //   return '90'
                  case '100':
                    return '100'
                }
              }
            }, // 刻度标签。
            axisTick: {
              show: true,
              splitNumber: 7,
              lineStyle: {
                color: '#1C3164', // 用颜色渐变函数不起作用
                width: 1
              },
              length: 4
            }, // 刻度样式
            splitLine: {
              show: true,
              length: 8,
              lineStyle: {
                color: '#1C3164' // 用颜色渐变函数不起作用
              }
            }, // 分隔线样式
            detail: {
              show: false
            },
            pointer: {
              show: false
            }
          },
          {
            // 指针上的圆
            type: 'pie',
            tooltip: {
              show: false
            },
            hoverAnimation: false,
            legendHoverLink: false,
            radius: ['0%', '20%'],
            center: ['50%', '50%'],
            label: {
              normal: {
                show: false
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              {
                value: 120,
                itemStyle: {
                  normal: {
                    color: 'rgb(14,31,73)'
                  }
                }
              }
            ]
          }
        ]
      }
      this.myChart.setOption(option)
    }
  }
}
</script>

<style scoped>
.tuopan {
  position: relative;
  width: 100%;
  height: 100%;
}
.main {
  height: 100%;
}
.tuopanItem {
  position: absolute;
  left: 0;
  top: 0;
}
.title {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  font-family: SourceHanSansSC-Regular;
  font-size: 28px;
  height: 40px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 40px;
  letter-spacing: 3px;
  color: #6ecaff;
}
</style>



