<template>
  <div class="FlipNum">
    <Flipper ref="flipperYear1" :isMax="true" />
    <Flipper ref="flipperYear2" :isMax="true" />
    <em>,</em>
    <Flipper ref="flipperYear3" :isMax="true" />
    <Flipper ref="flipperYear4" :isMax="true" />
    <Flipper ref="flipperMouth1" :isMax="true" />
    <em>,</em>
    <Flipper ref="flipperMouth2" :isMax="true" />
    <Flipper ref="flipperDay1" :isMax="true" />
    <Flipper ref="flipperDay2" :isMax="true" />
  </div>
</template>

<script>
import Flipper from './Flipper'
export default {
  name: 'FlipNum',
  data () {
    return {
      dayObjs: [],
      FlipValueOld: ''
    }
  },
  components: {
    Flipper
  },
  props: {
    FlipValue: {
      type: String,
      default: '00000000'
    }
  },
  watch: {
    FlipValue: {
      handler (newName, oldName) {
        if (oldName && oldName.length < 8) {
          oldName = this.fixTheValue(oldName)
        }
        if (newName.length < 8) {
          let base = this.fixTheValue(newName)
          this.run(oldName, base)
        }
        if (newName.length === 8) {
          this.run(oldName)
        }
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    fixTheValue (newName) {
      let header = '0'
      let base = newName
      for (let Num = base.length; Num < 8; Num++) {
        base = header + base
      }
      return base
    },
    init () {
      let nowDateTimeStr = this.FlipValue
      for (let i = 0; i < this.dayObjs.length; i++) {
        this.dayObjs[i].setFront(nowDateTimeStr[i])
      }
    },
    run (oldName, nowDate = '') {
      let nowDateTimeStr = nowDate === '' ? this.FlipValue : nowDate
      for (let i = 0; i < this.dayObjs.length; i++) {
        this.dayObjs[i].flipDown(oldName[i], nowDateTimeStr[i])
      }
    }
  },
  mounted () {
    this.dayObjs = [
      this.$refs.flipperYear1,
      this.$refs.flipperYear2,
      this.$refs.flipperYear3,
      this.$refs.flipperYear4,
      this.$refs.flipperMouth1,
      this.$refs.flipperMouth2,
      this.$refs.flipperDay1,
      this.$refs.flipperDay2
    ]
    this.init()
  }
}
</script>

<style>
.FlipNum {
  text-align: center;
  color: #63e7ff;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.FlipNum .M-Flipper {
  margin: 0 3px;
}
.FlipNum em {
  display: inline-block;
  line-height: 195px;
  font-size: 136px;
  font-style: normal;
  vertical-align: top;
}
</style>



