<template>
  <section class="chartsBox">
    <div :id="container" class="container" ref="reference"></div>
  </section>
</template>

<script>

// 图表基础引用
export default {
  name: 'polarAreaMap',
  data () {
    return {
      sourDate: null,
      myChart: null,
      container: Math.random()
        .toString(36)
        .substr(2)
    }
  },
  props: {
    ChartSour: {
      required: false,
      default: () => [],
      type: Array
    }
  },
  watch: {
    ChartSour: {
      handler (newName, oldName) {
        if (newName.length > 0) {
          let _this = this
          this.$nextTick(() => {
            _this.sourDate = JSON.parse(JSON.stringify(newName))
            _this.initChart(_this.sourDate)
          })
        }
      },
      immediate: true,
      deep: true
    }
  },
  mounted () {
    this.myChart = this.$echarts.init(document.getElementById(this.container))
  },
  methods: {
    initChart (ChartSour) {
      let color = [
        '#ff536e',
        '#feb33f',
        '#3ab9fe',
        '#1fd1b8',
        '#FD866A',
        '#9E87FF',
        '#58D5FF',
        '#00FFFF',
        '#44EAB1',
        '#7BDD43',
        '#FEBE12',
        '#EBEC2F',
        '#FF7C44',
        '#FA3E5F',
        '#6635EF',
        '#FFAFDA'
      ]
      let legendDate = []
      let legendDateMap = new Map()
      ChartSour.sort((a, b) => {
        return a.valuePer > b.valuePer ? -1 : 1
      })
      ChartSour.forEach((item, index) => {
        legendDate.push(item.name)
        legendDateMap.set(item.name, item.valuePer)
        item.itemStyle = {
          color: ''
        }
        item.dateIndex = index
        item.value = item.valuePer
        let target = index % color.length
        item.itemStyle.color = color[target]
      })
      let option = {
        title: [
          {
            text: '',
            textStyle: {
              fontSize: 16,
              color: 'black'
            },
            left: '2%'
          }
        ],
        color: color,
        legend: {
          orient: 'vertical',
          top: 'center',
          left: '45%',
          data: legendDate,
          formatter: function (parms) {
            return parms + ':' + legendDateMap.get(parms) + '%'
          },
          textStyle: {
            color: '#fff',
            fontSize: 16
          }
        },
        tooltip: {
          trigger: 'item',
          formatter: function (parms) {
            var str =
              parms.marker +
              '' +
              parms.data.name +
              '</br>' +
              '占比：' +
              parms.value +
              '%'
            return str
          }
        },
        series: [
          {
            name: '标题',
            type: 'pie',
            center: ['25%', '50%'],
            radius: ['0%', '65%'],
            clockwise: false, // 饼图的扇区是否是顺时针排布
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                fontSize: 20,
                position: 'outter',
                formatter: function (parms) {
                  if (parms.data.dateIndex < 2) {
                    return `${parms.data.name}(${parms.data.value}%)`
                  } else {
                    return `{x|${parms.data.name}(${parms.data.value}%)}`
                  }
                },
                rich: {
                  x: {
                    fontSize: 20,
                    align: 'center',
                    color: '#6ecaff',
                    padding: [0, 0, 0, 0]
                  }
                }
              }
            },
            labelLine: {
              normal: {
                length: 10,
                length2: 30,
                smooth: false,
                lineStyle: {
                  width: 0.5
                }
              }
            },
            data: ChartSour
          }
        ]
      }
      this.myChart.setOption(option)
    }
  }
}
</script>

<style scoped>
.container {
  width: 100%;
  height: 250px;
}
</style>



