import Vue from 'vue'
import Router from 'vue-router'

import main from '@/view/main'

import centerMap from '@/view/IntegratedOperation/center/centerMap'

import allView from '@/view/IntegratedOperation/allView'

import leftView from '@/view/IntegratedOperation/leftView'
import rightView from '@/view/IntegratedOperation/rightView'

import leftViewHeader from '@/view/IntegratedOperation/leftView/header'
import leftViewCenter from '@/view/IntegratedOperation/leftView/center'
import leftViewBottom from '@/view/IntegratedOperation/leftView/bottom'

import rightViewHeader from '@/view/IntegratedOperation/rightView/header'
import rightViewCenter from '@/view/IntegratedOperation/rightView/center'
import rightViewBottom from '@/view/IntegratedOperation/rightView/bottom'

import jifangView from '@/view/jifang'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  scrollBehavior: (to, from, savedPosition) => {
    return {}
  },
  routes: [

    { path: '/', component: main },
    // IT综合运行视图 -----------
    { path: '/allView', component: allView },

    { path: '/centerMap', component: centerMap },

    { path: '/left', component: leftView },
    { path: '/leftHeader', component: leftViewHeader },
    { path: '/leftCenter', component: leftViewCenter },
    { path: '/leftBottom', component: leftViewBottom },

    { path: '/right', component: rightView },
    { path: '/rightHeader', component: rightViewHeader },
    { path: '/rightCenter', component: rightViewCenter },
    { path: '/rightBottom', component: rightViewBottom },
    // IT综合运行视图 -----------
    { path: '/test', component: () => import('@/view/back/test') },
    { path: '/jifangView', component: jifangView },
    // 日常运维大屏 -----------
    { path: '/dailyLeft', component: () => import('@/view/DailyOperation/leftView') },
    { path: '/dailyLeftHeader', component: () => import('@/view/DailyOperation/leftView/header') },
    { path: '/dailyLeftCenter', component: () => import('@/view/DailyOperation/leftView/center') },
    { path: '/dailyLeftBotom', component: () => import('@/view/DailyOperation/leftView/bottom') },

    { path: '/dailyCenterBottom', component: () => import('@/view/DailyOperation/cenetrBottomView') },
    { path: '/dailyCenterHeader', component: () => import('@/view/DailyOperation/centerView/header') },
    { path: '/dailyCenter', component: () => import('@/view/DailyOperation/centerView/center') },
    { path: '/dailyBottom', component: () => import('@/view/DailyOperation/centerView/bottom') },

    { path: '/dailyRight', component: () => import('@/view/DailyOperation/rightView') },
    { path: '/dailyRightHeader', component: () => import('@/view/DailyOperation/rightView/header') },
    { path: '/dailyRightCenter', component: () => import('@/view/DailyOperation/rightView/center') },
    { path: '/dailyRightBottom', component: () => import('@/view/DailyOperation/rightView/bottom') },

    {
      path: '/dailyMain',
      redirect: '/dailyMain/map',
      component: () => import('@/view/DailyOperation/center/main'),
      children: [
        {
          path: '/dailyMain/map',
          name: 'dailyMainMap',
          component: () => import('@/view/DailyOperation/center/children/map')
        },
        {
          path: '/dailyMain/jifang/:type',
          name: 'dailyMainJifang',
          component: () => import('@/view/DailyOperation/center/children/jifangAll')
        },
        {
          path: '/dailyMain/jigui/:type',
          name: 'dailyMainJiGui',
          component: () => import('@/view/DailyOperation/center/children/jigui')
        },
        {
          path: '/dailyMain/tuopu/:type',
          name: 'dailyMainTuopu',
          component: () => import('@/view/DailyOperation/center/children/tuopu')
        }
      ]
    },
    // 日常运维大屏 -----------

    // 灾备切换大屏 -----------
    { path: '/ZBQHAll', component: () => import('@/view/zaiBeiQieHuanYunXinShiTu/allView') },
    { path: '/ZBQH', component: () => import('@/view/zaiBeiQieHuanYunXinShiTu/ZBQH') },
    { path: '/ZBQHLeft', component: () => import('@/view/zaiBeiQieHuanYunXinShiTu/left/Left') },
    { path: '/ZBQHLeft1', component: () => import('@/view/zaiBeiQieHuanYunXinShiTu/left/Left1') },
    { path: '/ZBQHLeft2', component: () => import('@/view/zaiBeiQieHuanYunXinShiTu/left/Left2') },
    { path: '/ZBQHLeft3', component: () => import('@/view/zaiBeiQieHuanYunXinShiTu/left/Left3') },
    { path: '/ZBQHCenter', component: () => import('@/view/zaiBeiQieHuanYunXinShiTu/Center') },
    { path: '/ZBQHRight', component: () => import('@/view/zaiBeiQieHuanYunXinShiTu/Right') },
    { path: '/ZBQHRight1', component: () => import('@/view/zaiBeiQieHuanYunXinShiTu/Right1') },
    { path: '/ZBQHRight2', component: () => import('@/view/zaiBeiQieHuanYunXinShiTu/Right2') },
    { path: '/ZBQHRight3', component: () => import('@/view/zaiBeiQieHuanYunXinShiTu/Right3') }
    // 灾备切换大屏 -----------
  ]
})



