<template>
  <section
    class="footerBox swiperBox"
    @mouseover="stopTimeToNext()"
    @mouseout="setTimeToNext()"
  >
    <section class="swiper">
      <transition
        :name="slideName"
        v-for="(slide, index) in slideLisy"
        :key="slide"
      >
        <section
          class="swiperItem"
          v-show="activeIndex.includes(index)"
          :class="getSwiperItemClass(index)"
          :style="{ backgroundImage: ' url(' + charBG + ')', order: slide }"
        >
          <lineCharts :sourDate="chartsDate[slide]"></lineCharts>
        </section>
      </transition>
    </section>
    <div class="swiper-button-prev swiperDao" @click="next"></div>
    <div class="swiper-button-next swiperDao" @click="prev"></div>
  </section>
</template>

<script>
import { mapGetters } from "vuex";
import "animate.css";
import charBG from "@/assets/image/shucai/charBG.png";
import lineCharts from "@/components/lineCharts";
// import Worker from '../../../../../static/work/mapWork.js'
// 定时器
// import setIntervalObject from '@/mixins/setIntervalObject'
const showSize = 4;
function moveElement(arr, n) {
  if (Math.abs(n) > arr.length) n = n % arr.length;
  let back = arr.slice(-n).concat(arr.slice(0, -n));
  return back;
}
function getSize(num) {
  let arr = [];
  for (let i = 0; i < num; i++) {
    arr.push(i);
  }
  return arr.splice(-showSize);
}

export default {
  name: "userInfo",
  // mixins: [setIntervalObject],
  components: {
    lineCharts
  },
  props: {
    slideAll: {
      required: false,
      default: true,
      type: Boolean
    },
    playSpeed: {
      required: false,
      default: 10000,
      type: Number
    }
  },
  data() {
    return {
      charBG,
      slideName: "slide",
      showCharts: true,
      // eslint-disable-next-line no-undef
      slideLisy: window.footerCharts.map((item, index) => {
        return index;
      }),
      // eslint-disable-next-line no-undef
      slideLisyHistory: window.footerCharts.map((item, index) => {
        return index;
      }),
      // eslint-disable-next-line no-undef
      chartsDate: JSON.parse(JSON.stringify(window.footerCharts)),
      mySwiper: {},
      showSize,
      indicateType: new Set(),
      activeIndexMax: showSize,
      activeIndex: getSize(showSize),
      setIntervalObjectNext: null,
      doNextTime: 0
      // webWorkerObject: new Worker()
    };
  },
  computed: {
    ...mapGetters(["IntegratedOperationBottom"])
  },
  watch: {
    IntegratedOperationBottom: {
      handler(newName, oldName) {
        if (newName !== undefined && newName.length > 0) {
          let _this = this;
          this.$nextTick(() => {
            _this.initCharts(newName);
          });
        }
      },
      immediate: true,
      deep: true
    },
    showCharts() {
      setTimeout(() => {
        this.showCharts = true;
      }, 500);
    }
  },
  // beforeMount () {
  //   this.getCharsDate()
  // },
  mounted() {
    if (window.footerCharts) {
      // eslint-disable-next-line no-undef
      this.slideLisy = window.footerCharts.map((item, index) => {
        return index;
      });
      // eslint-disable-next-line no-undef
      this.slideLisyHistory = window.footerCharts.map((item, index) => {
        return index;
      });
      // eslint-disable-next-line no-undef
      this.chartsDate = JSON.parse(JSON.stringify(window.footerCharts));
    }
    this.setTimeToNext();
    // let _this = this
    // setInterval(() => {
    //   let m = 10000
    //   let n = 100
    //   const test = [
    //     {'dateTime': 1599721361026, 'type': 'bottom', 'value': Math.floor(Math.random() * (m - n) + n), 'group': '深圳金田营业部', 'indicateName': '各银行转入金额'},
    //     {'dateTime': 1599721361027, 'type': 'bottom', 'value': Math.floor(Math.random() * (m - n) + n), 'group': '揭阳临江南营业部', 'indicateName': '各银行转入金额'},
    //     {'dateTime': 1599721361027, 'type': 'bottom', 'value': Math.floor(Math.random() * (m - n) + n), 'group': '杭州市心北营业部', 'indicateName': '各银行转入金额'},
    //     {'dateTime': 1599721361027, 'type': 'bottom', 'value': Math.floor(Math.random() * (m - n) + n), 'group': '南京中央路营业部', 'indicateName': '各银行转入金额'},
    //     {'dateTime': 1599721361027, 'type': 'bottom', 'value': Math.floor(Math.random() * (m - n) + n), 'group': '杭州中山北路营业部', 'indicateName': '各银行转入金额'}
    //   ]
    //   _this.initCharts(test)
    // }, 1000)
  },
  beforeDestroy() {
    this.stopTimeToNext();
  },
  methods: {
    getSwiperItemClass(index) {
      return "SwiperItem" + this.activeIndex.indexOf(index);
    },
    initCharts(sourDate) {
      let sour = sourDate || [];
      let dateBase = JSON.parse(JSON.stringify(this.chartsDate));
      // if (this.webWorkerObject) {
      //   this.webWorkerObject.postMessage('123123')
      //   this.webWorkerObject.onmessage = function (event) {
      //     console.log(event)
      //   }
      //   return false
      // }
      let keysMap = new Map();
      let dateBaseMap = new Map();
      dateBase.forEach(item => {
        dateBaseMap.set(item.title, item);
        item.indicateNameList.map(indicateName => {
          keysMap.set(indicateName, item.title);
        });
      });
      if (sour && sour.length > 0) {
        sour.forEach(element => {
          if (!this.indicateType.has(element.indicateName))
            this.indicateType.add(element.indicateName);
          if (keysMap.get(element.indicateName)) {
            let title = keysMap.get(element.indicateName);
            let base = dateBaseMap.get(title);
            if (base.type === "interval") {
              let target = base.result.findIndex(item => {
                return item.group === element.group;
              });
              if (target !== -1) {
                base.result[target] = element;
              } else {
                base.result.sort((a, b) => {
                  return a.dateTime - b.dateTime;
                });
                if (base.result.length >= 5) {
                  base.result.shift();
                }
                base.result.push(element);
              }
            } else {
              if (base.result.length > 20 * this.indicateType.size) {
                base.result.shift();
              }
              base.result.push(element);
            }
            dateBaseMap.set(title, base);
          }
        });
        let afterFix = [];
        dateBaseMap.forEach((item, keys) => {
          let date = dateBaseMap.get(keys);
          afterFix.push(date);
        });
        this.chartsDate = afterFix;
      }
    },
    getCharsDate() {
      this.$axios({
        method: "get",
        url: "/api/mapFooter",
        data: {}
      })
        .then(response => {
          let sour = response.data.responseBody || [];
          this.chartsDate = sour;
        })
        .catch(() => {
          console.error("请求失败!");
        });
    },
    next() {
      this.slideName = "slide";
      // this.showCharts = false
      // let oneStep = this.slideAll ? showSize : 1
      let oneStep = 1;
      let sour = moveElement(this.slideLisyHistory, -oneStep);
      this.slideLisyHistory = JSON.parse(JSON.stringify(sour));
      // this.activeIndex = []
      let _this = this;
      this.doNextTime && clearTimeout(this.doNextTime);
      this.doNextTime = setTimeout(() => {
        _this.activeIndex = sour.splice(0, showSize);
      }, 500);
    },
    prev() {
      this.slideName = "slideRight";
      let oneStep = 1;
      let sour = moveElement(this.slideLisyHistory, oneStep);
      this.slideLisyHistory = JSON.parse(JSON.stringify(sour));
      // this.activeIndex = []
      let _this = this;
      this.doNextTime && clearTimeout(this.doNextTime);
      this.doNextTime = setTimeout(() => {
        _this.activeIndex = sour.splice(0, showSize);
      }, 500);
    },
    stopTimeToNext() {
      window.clearInterval(this.setIntervalObjectNext);
    },
    setTimeToNext() {
      this.stopTimeToNext();
      this.setIntervalObjectNext = setInterval(() => {
        this.next();
      }, this.playSpeed);
    }
  }
};
</script>
<style scoped>
@import "animate.css";
.swiper {
  height: 100%;
  margin: auto;
  width: 100%;
  overflow: hidden;
  position: relative;
}
.SwiperItem0 {
  transition-duration: 1s;
  position: absolute;
  left: 0;
  top: 0;
}
.SwiperItem1 {
  transition-duration: 1s;
  position: absolute;
  left: 1221px;
  top: 0;
}
.SwiperItem2 {
  transition-duration: 1s;
  position: absolute;
  left: 2442px;
  top: 0;
}
.SwiperItem3 {
  transition-duration: 1s;
  position: absolute;
  left: 3663px;
  top: 0;
}
.swiperItem {
  width: 1180px;
  height: 695px;
  /* transition-duration: 0s; */
  background-size: cover;
  background-repeat: no-repeat;
}
.swiperBox {
  position: relative;
}
.swiper-slide {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-weight: bold;
}
.swiperDao {
  cursor: pointer;
  width: 430px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
  position: absolute;
  top: 0;
}
.swiper-button-prev {
  left: -430px;
}
.swiper-button-next {
  right: -430px;
}

.footerBox {
  position: absolute;
  left: 432px;
  bottom: 80px;
  width: 4884px;
  height: 695px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.slide-enter-active {
  animation: 1s fadeInRight;
}
.slide-enter-out {
  /* animation: 0.5s fadeOutLeft; */
}

.slideRight-enter-active {
  animation: 1s fadeInLeft;
}
.slideRight-enter-out {
  /* animation: 0.5s fadeOutRight; */
}
@keyframes frontFlipDown {
  0% {
    transform: perspective(215px) rotateX(0deg);
  }

  100% {
    transform: perspective(215px) rotateX(-180deg);
  }
}

@keyframes backFlipDown {
  0% {
    transform: perspective(215px) rotateX(180deg);
  }

  100% {
    transform: perspective(215px) rotateX(0deg);
  }
}
</style>



