<template>
  <div class="container">
    <div class="cubeBox">
      <div class="cubeBoxPre">
        <div class="tap mainFont">{{present}}%</div>
        <div class="yuanzhu bar" :style="{'height':height+'px'}">
          <span :style="{'height':height+10+'px'}"></span>
        </div>
      </div>
      <img :src="cubeBox" style="display: block;margin: auto;" />
    </div>
    <div class="title">{{title}}</div>
  </div>
</template>

<script>
import cubeBox from '@/assets/image/shucai/cubeBox.png'
export default {
  name: 'cubeBox3d',
  props: {
    title: {
      type: String,
      default: '虚拟机'
    },
    present: {
      type: Number,
      default: 10
    }
  },
  data () {
    return {
      max: 110,
      cubeBox
    }
  },
  computed: {
    height () {
      return this.max * Number(this.present) * 0.01
    }
  }
}
</script>

<style scoped>
.container {
  position: relative;
  height: 100%;
}
.cubeBox {
  position: relative;
  width: 90px;
  margin: 0 auto;
}
.tap {
  position: absolute;
  font-size: 36px;
  top: 0;
  right: -85px;
}
.cubeBoxPre {
  position: absolute;
  bottom: 37px;
  left: 18px;
}
.yuanzhu {
  transition-duration: 1s;
  opacity: 0.9;
  position: relative;
  height: 0px;
  width: 52px;
  margin: 0;
  z-index: 999;
  background-image: linear-gradient(
      90deg,
      rgba(58, 185, 254, 0.88) 0%,
      rgba(58, 206, 254, 0.88) 16%,
      rgba(58, 219, 254, 0.88) 42%,
      rgba(58, 185, 254, 0.88) 65%,
      rgba(28, 140, 255, 0.88) 100%
    ),
    linear-gradient(#061643, #061643);
}

.yuanzhu:before {
  position: absolute;
  top: -10px;
  content: "";
  display: block;
  width: 50px;
  height: 15px;
  border-radius: 50%;
  z-index: 99;
  background-image: linear-gradient(
      90deg,
      rgba(58, 185, 254, 0.88) 0%,
      rgba(58, 206, 254, 0.88) 16%,
      rgba(58, 219, 254, 0.88) 42%,
      rgba(58, 185, 254, 0.88) 65%,
      rgba(28, 140, 255, 0.88) 100%
    ),
    linear-gradient(#061643, #061643);
  background-blend-mode: normal, normal;
  border: solid 1px rgba(42, 107, 214, 0.5);
}

.yuanzhu:after {
  position: absolute;
  bottom: -10px;
  content: "";
  display: block;
  width: 50px;
  height: 15px;
  border-radius: 50%;
  z-index: -1;
  background-image: linear-gradient(
      90deg,
      rgba(58, 185, 254, 0.88) 0%,
      rgba(58, 206, 254, 0.88) 16%,
      rgba(58, 219, 254, 0.88) 42%,
      rgba(58, 185, 254, 0.88) 65%,
      rgba(28, 140, 255, 0.88) 100%
    ),
    linear-gradient(#061643, #061643);
  border: solid 1px transparent;
}
.title {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  font-family: SourceHanSansSC-Regular;
  font-size: 28px;
  height: 40px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 40px;
  letter-spacing: 3px;
  color: #6ecaff;
}

.bar span {
  position: absolute;
  transition-duration: 1s;
  display: block;
  width: 100%;
  height: 0;
  -webkit-border-radius: 16px;
  border-radius: 16px;
  bottom: -8px;
  left: 0;
  background: url("data:image/png;base64,")
    0 0;
  -webkit-animation: sparkle 1500ms linear infinite;
  -moz-animation: sparkle 1500ms linear infinite;
  -o-animation: sparkle 1500ms linear infinite;
  animation: sparkle 1500ms linear infinite;
  opacity: 0.2;
}
@-webkit-keyframes sparkle {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 0 -64px;
  }
}
@-moz-keyframes sparkle {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 0 -64px;
  }
}
@-o-keyframes sparkle {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 0 -64px;
  }
}
@keyframes sparkle {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 0 -64px;
  }
}
</style>



