<template>
  <section
    class="mainBox"
    :style="{ backgroundImage: ' url(' + backgroundImage + ')' }"
  >
    <section class="systemInfo" v-if="systemInfo.length > 0">
      <div
        class="systemInfoItem"
        v-for="systemInfoItem in systemInfo"
        :key="systemInfoItem.indicateName"
      >
        <div class="systemInfoNum mainFont">{{ systemInfoItem.value }}</div>
        <div class="systemInfoName">{{ systemInfoItem.indicateName }}</div>
      </div>
      <div class="systemInfoItem">
        <div class="systemInfoNum mainFont">{{ allNum }}</div>
        <div class="systemInfoName">当前运行系统总数</div>
      </div>
    </section>
    <section class="animate" v-if="infoList.length > 0">
      <div class="allNum">
        <div class="ballNum">{{ allNum }}</div>
        <div class="Name">应用系统总数</div>
      </div>
      <div
        v-for="(item, index) in infoListBase"
        :key="item"
        :class="['systemInfoItem ball', 'ball' + (index + 1), running]"
        @mouseout="running = 'play'"
        @mouseover="running = 'pause'"
      >
        <div class="Num">
          <span class="ballNum">{{ infoList[index].value }}</span>
          <img :src="v3BG" />
        </div>
        <div class="Name">{{ infoList[index].indicateName }}</div>
      </div>
    </section>
  </section>
</template>

<script>
import backgroundImage from "@/assets/image/background/3-3.jpg";
// 定时器
// import setIntervalObject from '@/mixins/setIntervalObject'

import websockObject from "@/mixins/websock";
import v3BG from "@/assets/image/background/v3BG.png";
export default {
  mixins: [websockObject],
  name: "",
  data() {
    return {
      backgroundImage,
      v3BG,
      running: "",
      systemInfo: [],
      infoList: [],
      allNum: 0,
      infoListBase: [1, 2, 3, 4, 5, 6],
      // eslint-disable-next-line no-undef
      orderList: righBottom
    };
  },
  mounted() {
    // eslint-disable-next-line no-undef
    this.wsuri = BaseUrl.rightBottom.websockUrl;
    // this.useTheTestDate()
  },
  methods: {
    useTheTestDate() {
      let m = 100;
      let n = 0;
      const base = [
        {
          dateTime: 1599721695594,
          type: "top",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "核心交易系统客户总量",
          group: "核心交易系统客户总量"
        },
        {
          dateTime: 1599723543121,
          外围交易: Math.floor(Math.random() * (m - n) + n),
          totalSize: Math.floor(Math.random() * (m - n) + n),
          基础支撑: Math.floor(Math.random() * (m - n) + n),
          员工服务: Math.floor(Math.random() * (m - n) + n),
          交易服务: Math.floor(Math.random() * (m - n) + n),
          核心交易: Math.floor(Math.random() * (m - n) + n),
          type: "bottom",
          客户服务: Math.floor(Math.random() * (m - n) + n),
          indicateName: "业务系统统计",
          group: "业务系统统计"
        }
      ];
      this.getWebsocketMsg(base);
    },
    getWebsocketMsg(sourDate) {
      let self = this;
      if (sourDate.length > 0) {
        sourDate.forEach(element => {
          if (self.orderList.headerIndicate.includes(element.indicateName)) {
            let target = self.systemInfo.findIndex(item => {
              return element.indicateName === item.indicateName;
            });
            if (target !== -1) {
              self.$set(self.systemInfo, target, element);
            } else {
              self.systemInfo.push(element);
            }
          }
          if (self.orderList.runningBor.indicateName === element.indicateName) {
            self.orderList.runningBor.showType.forEach(type => {
              let typeItem = {
                indicateName: type,
                value: element[type]
              };
              let target = self.infoList.findIndex(item => {
                return type === item.indicateName;
              });
              if (target !== -1) {
                self.$set(self.infoList, target, typeItem);
              } else {
                self.infoList.push(typeItem);
              }
              let sourAllNum = 0;
              self.infoList.forEach((element, index) => {
                sourAllNum = sourAllNum + element.value;
              });
              self.allNum = sourAllNum;
            });
          }
        });
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    },
    async getSystemNum() {
      return this.$axios({
        method: "get",
        url: "/api/systemNum",
        data: {}
      });
    },
    async getSystemInfo() {
      return this.$axios({
        method: "get",
        url: "/api/systemInfo",
        data: {}
      });
    },
    async getCharsDate(sourDate) {
      let sourFormSystemNum = await this.getSystemNum();
      if (sourFormSystemNum.data.responseBody) {
        let sour = sourFormSystemNum.data.responseBody || [];
        let sourAllNum = 0;
        sour.forEach((element, index) => {
          sourAllNum = sourAllNum + element.value;
          this.$set(this.infoList, index, element);
        });
        this.allNum = sourAllNum;
      }
      let sourFormSystemInfo = await this.getSystemInfo();
      if (sourFormSystemInfo.data.responseBody) {
        let sour = sourFormSystemInfo.data.responseBody || [];
        this.systemInfo = sour;
      }
    }
  }
};
</script>

<style scoped>
.mainChartBox .title {
  left: 0;
}
.systemInfo {
  position: absolute;
  width: 1454px;
  top: 25px;
  left: 50%;
  margin-left: -727px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: left;
}
.systemInfoName {
  font-size: 44px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 1;
  letter-spacing: 0px;
  color: #e0e8ff;
}
.systemInfoNum {
  font-size: 72px;
  font-weight: bold;
  font-stretch: normal;
  line-height: 1;
  letter-spacing: 7px;
}
.allNum {
  position: absolute;
  width: 500px;
  text-align: center;
  left: 50%;
  margin-left: -250px;
  top: 150px;
}
.allNum .ballNum {
  position: relative;
}
.allNum .Name {
  width: 100%;
}
.Num {
  position: relative;
}
.Num img {
  display: block;
  margin: auto;
}
.Name {
  width: 242px;
  font-size: 40px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 4px;
  color: #6ecaff;
}
.ballNum {
  transition-duration: 1s;
  position: absolute;
  left: 0;
  top: 0;
  text-align: center;
  width: 100%;
  font-size: 92px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 96px;
  letter-spacing: 0px;
  color: #ace5ff;
}

/* 旋转动画 */
.animate {
  width: 1454px;
  height: 608px;
  border-radius: 50%;
  position: absolute;
  left: 230px;
  bottom: 150px;
}
@keyframes animX {
  0% {
    left: 0px;
  }
  100% {
    left: 1385px;
  }
}
@keyframes animY {
  0% {
    top: -50px;
  }
  100% {
    top: 485px;
  }
}
@keyframes scale {
  0% {
    transform: scale(0.7);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(0.7);
  }
}
.ball {
  cursor: pointer;
  width: 100px;
  height: 100px;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 12px;
}

/* 6个图x和y轴动画加起来是18s , 18s/6 等于 3s
每个球y轴动画延迟 从0递减3s,x轴与y轴相差动画时长的一半（9s/2） */
.play {
  animation: animX 36s cubic-bezier(0.36, 0, 0.64, 1) -18s infinite alternate,
    animY 36s cubic-bezier(0.36, 0, 0.64, 1) 0s infinite alternate,
    scale 72s cubic-bezier(0.36, 0, 0.64, 1) 0s infinite alternate;
}
.restart {
  animation: animX 36s cubic-bezier(0.36, 0, 0.64, 1) -18s infinite alternate,
    animY 36s cubic-bezier(0.36, 0, 0.64, 1) 0s infinite alternate,
    scale 72s cubic-bezier(0.36, 0, 0.64, 1) 0s infinite alternate;
}
.pause {
  animation-play-state: paused !important;
}
.ball1 {
  animation: animX 36s cubic-bezier(0.36, 0, 0.64, 1) -18s infinite alternate,
    animY 36s cubic-bezier(0.36, 0, 0.64, 1) 0s infinite alternate,
    scale 72s cubic-bezier(0.36, 0, 0.64, 1) 0s infinite alternate;
}
.ball2 {
  animation: animX 36s cubic-bezier(0.36, 0, 0.64, 1) -30s infinite alternate,
    animY 36s cubic-bezier(0.36, 0, 0.64, 1) -12s infinite alternate,
    scale 72s cubic-bezier(0.36, 0, 0.64, 1) -12s infinite alternate;
}
.ball3 {
  animation: animX 36s cubic-bezier(0.36, 0, 0.64, 1) -42s infinite alternate,
    animY 36s cubic-bezier(0.36, 0, 0.64, 1) -24s infinite alternate,
    scale 72s cubic-bezier(0.36, 0, 0.64, 1) -24s infinite alternate;
}
.ball4 {
  animation: animX 36s cubic-bezier(0.36, 0, 0.64, 1) -54s infinite alternate,
    animY 36s cubic-bezier(0.36, 0, 0.64, 1) -36s infinite alternate,
    scale 72s cubic-bezier(0.36, 0, 0.64, 1) -36s infinite alternate;
}
.ball5 {
  animation: animX 36s cubic-bezier(0.36, 0, 0.64, 1) -66s infinite alternate,
    animY 36s cubic-bezier(0.36, 0, 0.64, 1) -48s infinite alternate,
    scale 72s cubic-bezier(0.36, 0, 0.64, 1) -48s infinite alternate;
}
.ball6 {
  animation: animX 36s cubic-bezier(0.36, 0, 0.64, 1) -78s infinite alternate,
    animY 36s cubic-bezier(0.36, 0, 0.64, 1) -60s infinite alternate,
    scale 72s cubic-bezier(0.36, 0, 0.64, 1) -60s infinite alternate;
}
</style>



