export default {
  state: {
    KcmimDeaLindicate: [],
    FinanceDeaLindicate: []
  },
  mutations: {
    setKcmimDeaLindicate (state, data) {
      state.KcmimDeaLindicate = data
    },
    setFinanceDeaLindicate (state, data) {
      state.FinanceDeaLindicate = data
    }
  },
  getters: {
    getKcmimDeaLindicate: state => state.KcmimDeaLindicate,
    getFinanceDeaLindicate: state => state.FinanceDeaLindicate
  },
  actions: {
    setTheFinanceDeaLindicateList: ({ commit }, data) => {
      return commit('setFinanceDeaLindicate', data)
    },
    setTheKcmimDeaLindicateList: ({ commit }, data) => {
      return commit('setKcmimDeaLindicate', data)
    }
  }
}



