<template>
  <section class="headerBox">
    <div class="FlipNumBox">
      <div class="titleHeader">当日成交总金额（单位：万元）</div>
      <section class="FlipNum">
        <FlipNum :FlipValue="allDealNum"></FlipNum>
      </section>
    </div>
    <div class="DealBox">
      <div class="DealInfoBox">
        <div class="titleHeader">上证指数</div>
        <!-- <div class="DealPoint" :class="{'down':!shangHaiUp,'up':shangHaiUp}">
          <img :src="shangHaiUp ? redIcon : greenIcon" class="icon"/>
          {{shangHai.addNum}} ({{ (!shangHaiUp ? '-': '+')+shangHai.addPresent}}%)
        </div> -->
        <div class="DealAll" :class="{ down: !shangHaiUp, up: shangHaiUp }">
          {{ shangHai }}
        </div>
      </div>
      <div class="DealInfoBox">
        <div class="titleHeader">深证成指</div>
        <!-- <div class="DealPoint" :class="{'down':!shenZhneUp,'up':shenZhneUp}">
          <img :src="shenZhneUp ? redIcon : greenIcon" class="icon"/>
          {{shenZhen.addNum}} ({{ (!shenZhneUp ? '-': '+')+shenZhen.addPresent}}%)
        </div> -->
        <div class="DealAll" :class="{ down: !shenZhneUp, up: shenZhneUp }">
          {{ shenZhen }}
        </div>
      </div>
    </div>
    <div class="FlipNumBox">
      <div class="titleHeader">当日委托笔数（单位：笔）</div>
      <section class="FlipNum">
        <FlipNum :FlipValue="entrustNum"></FlipNum>
      </section>
    </div>
  </section>
</template>

<script>
/* eslint-disable eqeqeq */
import { mapGetters } from "vuex";
import FlipNum from "@/components/FlipNum";
import greenIcon from "@/assets/image/shucai/v2_green.png";
import redIcon from "@/assets/image/shucai/v2_red.png";
// import setIntervalObject from '@/mixins/setIntervalObject'
const TYPE = {
  当日成交总金额: "allDealNum",
  当日委托笔数: "entrustNum",
  上证指数: "shangHai",
  深圳指数: "shenZhen"
};
export default {
  name: "mainHeader",
  // mixins: [setIntervalObject],
  data() {
    return {
      greenIcon,
      redIcon,
      TYPE,
      allDealNum: "00000000",
      entrustNum: "00000000",
      shangHai: 0,
      shenZhen: 0,
      shangHaiUp: true,
      shenZhneUp: true
    };
  },
  computed: {
    ...mapGetters(["IntegratedOperationTop"])
  },
  watch: {
    IntegratedOperationTop: {
      handler(newName, oldName) {
        if (newName !== undefined && newName.length > 0) {
          for (let element of newName) {
            if (
              element.indicateName == "上证指数" ||
              element.group.indexOf("上证") !== -1
            ) {
              this.shangHai = element.value.toFixed(2);
              this.shangHaiUp = element.colorState === 2;
              continue;
            }
            if (
              element.indicateName == "深圳指数" ||
              element.group.indexOf("深圳") !== -1
            ) {
              this.shenZhen = element.value.toFixed(2);
              this.shenZhneUp = element.colorState === 2;
              continue;
            }
            if (
              element.indicateName == "当日成交总金额" ||
              element.indicateName.indexOf("当日成交总金额") !== -1
            ) {
              this.allDealNum = element.value + "";
              continue;
            }
            if (
              element.indicateName == "当日委托笔数" ||
              element.indicateName.indexOf("当日委托笔数") !== -1
            ) {
              this.entrustNum = element.value + "";
              continue;
            }
          }
        }
      },
      immediate: true,
      deep: true
    }
    // shangHai: {
    //   handler (newName, oldName) {
    //     if (oldName) {
    //       this.shangHaiUp = newName > oldName
    //     }
    //   }
    // },
    // shenZhen: {
    //   handler (newName, oldName) {
    //     if (oldName) {
    //       this.shenZhneUp = newName > oldName
    //     }
    //   }
    // }
  },
  components: {
    FlipNum
  },
  mounted() {
    // this.loadSpeed = 10000
    // this.getCharsDate()
  },
  methods: {
    updatedDate(element) {
      if (element.indicateName === "上证指数") {
        this.shangHai = element.value;
      } else if (element.indicateName === "深圳指数") {
        this.shenZhen = element.value;
      } else if (element.indicateName === "当日成交总金额") {
        this.allDealNum = element.value + "";
      } else if (element.indicateName === "当日委托笔数") {
        this.entrustNum = element.value + "";
      }
    },
    getCharsDate(sourDate) {
      let sour = sourDate || [];
      if (sour && sour.length > 0) {
        sour.forEach(element => {
          if (element.indicateName === "上证指数") {
            this.shangHai = element.value;
          } else if (element.indicateName === "深圳指数") {
            this.shenZhen = element.value;
          } else if (element.indicateName === "当日成交总金额") {
            this.allDealNum = element.value + "";
          } else if (element.indicateName === "当日委托笔数") {
            this.entrustNum = element.value + "";
          }
        });
      }
    }
  }
};
</script>

<style scoped>
.up {
  background-image: -webkit-linear-gradient(bottom, #ff536e, #e62d2d);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.down {
  background-image: -webkit-linear-gradient(bottom, #31ff58, #08d943);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.headerBox {
  position: absolute;
  left: 92px;
  top: 403px;
  width: 5584px;
  height: 359px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.FlipNumBox {
  width: 1821px;
  height: 359px;
  text-align: left;
}
.titleHeader {
  font-size: 88px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 1;
  letter-spacing: 0px;
  color: #e0e8ff;
}
.FlipNum {
  padding-top: 20px;
}
.DealBox {
  flex: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 359px;
  padding: 0 205px;
}
.DealInfoBox {
  width: 50%;
  text-align: left;
  font-size: 60px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 96px;
  letter-spacing: 0px;
}
.DealPoint {
  padding: 20px 0;
}
.DealAll {
  font-size: 136px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 7px;
  font-weight: bold;
  line-height: 235px;
  height: 235px;
}
</style>



