      let self = this;
      if (self.fristBack) {
        let baseDate = {
          top: [],
          middle: [],
          bottom: []
        };
        for (let sourDateItem of sourDate) {
          baseDate[sourDateItem.type].push(sourDateItem);
        }
        self.setIntegratedOperation(baseDate);
      } else if (sourDate.length > 0) {
        let toBottom = [];
        let toHeader = [];
        for (let sourDateItem of sourDate) {
          if (sourDateItem.type === "top") {
            toHeader.push(sourDateItem);
          }
          if (sourDateItem.type === "middle") {
            self.$nextTick(() => {
              self.$refs.mapPoint &&
                self.$refs.mapPoint.updateLine(sourDateItem);
            });
          }
          if (sourDateItem.type === "bottom") {
            toBottom.push(sourDateItem);
          }
        }
        if (toHeader.length > 0) {
          self.setIntegratedOperationHeader(toHeader);
        }

        if (toBottom.length > 0) {
          toBottom.sort((a, b) => {
            return a.dateTime - b.dateTime;
          });
          self.setIntegratedOperationBottom(toBottom);
        }
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
