<template>
  <section class="mainBox">
    <div id="container"></div>
  </section>
</template>

<script>
/* eslint-disable no-undef */
/* eslint-disable new-cap */
/* eslint-disable camelcase */
/* eslint-disable no-redeclare */
export default {
  name: "THREETest",
  data() {
    return {
      camera: null,
      scene: null,
      renderer: null,
      mesh: null,
      keyboard: new KeyboardState(),
      controls: null,
      targetList: [],
      projector: null,
      SCREEN_WIDTH: 0,
      SCREEN_HEIGHT: 0,
      mouse: { x: 0, y: 0 }
    };
  },
  methods: {
    init() {
      let container = document.getElementById("container");
      this.scene = new THREE.Scene();
      this.SCREEN_WIDTH = container.clientWidth;
      this.SCREEN_HEIGHT = container.clientHeight;
      this.camera = new THREE.PerspectiveCamera(
        100,
        container.clientWidth / container.clientHeight,
        0.1,
        2000
      );
      this.camera.position.set(0, 500, 550);
      this.camera.lookAt(this.scene.position);

      this.renderer = new THREE.WebGLRenderer({ antialias: true });
      this.renderer.setSize(container.clientWidth, container.clientHeight);
      container.appendChild(this.renderer.domElement);

      var directionalLight = new THREE.DirectionalLight(0xffffff, 0.3); // 模拟远处类似太阳的光源
      directionalLight.color.setHSL(0.1, 1, 0.95);
      directionalLight.position.set(0, 200, 0).normalize();
      this.scene.add(directionalLight);

      var ambient = new THREE.AmbientLight(0xffffff, 1); // AmbientLight,影响整个场景的光源
      ambient.position.set(0, 0, 0);
      this.scene.add(ambient);

      this.controls = new THREE.OrbitControls(
        this.camera,
        this.renderer.domElement
      );
      this.initModel();
      this.projector = new THREE.Projector();
      // when the mouse moves, call the given function
      document.addEventListener("mousedown", this.onDocumentMouseDown, false);

      // when the mouse moves, call the given function
      document.addEventListener("mousemove", this.onDocumentMouseMove, false);
    },
    update() {
      this.keyboard.update();

      if (this.keyboard.pressed("left") || this.keyboard.pressed("A")) {
        this.camera.translateX(-5);
      }

      if (this.keyboard.pressed("right") || this.keyboard.pressed("D")) {
        this.camera.translateX(5);
      }

      if (this.keyboard.pressed("up") || this.keyboard.pressed("W")) {
        this.camera.translateZ(-5);
      }

      if (this.keyboard.pressed("down") || this.keyboard.pressed("S")) {
        this.camera.translateZ(5);
      }

      if (this.keyboard.pressed("R")) {
        this.camera.translateY(-5);
      }

      if (this.keyboard.pressed("F")) {
        this.camera.translateY(5);
      }

      if (this.keyboard.pressed("Q")) {
        this.controls.pan(new THREE.Vector3(-10, 0, 0));
      }

      if (this.keyboard.pressed("E")) {
        this.controls.pan(new THREE.Vector3(10, 0, 0));
      }

      this.controls.update();
    },
    animate: function() {
      requestAnimationFrame(this.animate);
      this.renderer.render(this.scene, this.camera);
      this.update();
    },
    initBase() {
      this.createFloor(1024, 800);
      let StartWall = this.createCubeWall(
        1024,
        130,
        0,
        65,
        400,
        0,
        0,
        0,
        "墙面"
      );
      let StartWallDoor = this.createCubeWall(
        100,
        128,
        0,
        31,
        400,
        0,
        0,
        0,
        "门"
      );
      let StartWallWindowOne = this.createCubeWall(
        350,
        50,
        -250,
        60,
        400,
        0,
        0,
        0,
        "窗户"
      );
      let StartWallWindowTwo = this.createCubeWall(
        350,
        50,
        250,
        60,
        400,
        0,
        0,
        0,
        "窗户"
      );
      let objestList = [];
      objestList.push(StartWallDoor);
      objestList.push(StartWallWindowOne);
      objestList.push(StartWallWindowTwo);
      let StartWallCut = this.createResultBsp(StartWall, objestList);
      this.scene.add(StartWallCut);
      let StartWallWindowOneSave = this.createCubeWindow(
        350,
        50,
        -250,
        60,
        400,
        0,
        0,
        0,
        "窗户"
      );
      let StartWallWindowTwoSave = this.createCubeWindow(
        350,
        50,
        250,
        60,
        400,
        0,
        0,
        0,
        "窗户"
      );
      this.scene.add(StartWallWindowOneSave);
      this.scene.add(StartWallWindowTwoSave);
      this.createDoor_left(100, 100, 0, 0, -50, 50, 400, 0, "左门");

      let EndWall = this.createCubeWall(
        1024,
        130,
        0,
        65,
        -400,
        0,
        0,
        0,
        "墙面"
      );
      this.scene.add(EndWall);
      let LeftWall = this.createCubeWall(
        800,
        130,
        512,
        65,
        0,
        0,
        Math.PI / 2,
        0,
        "墙面"
      );
      this.scene.add(LeftWall);
      let RightWall = this.createCubeWall(
        800,
        130,
        -512,
        65,
        0,
        0,
        Math.PI / 2,
        0,
        "墙面"
      );
      this.scene.add(RightWall);
    },
    initModel() {
      this.initBase();
      /*
        size:
          x  -480 ~ 480
          y  0
          z  350 ~ -350
      */
      let model = [
        {
          uuid: "1",
          id: 1,
          name: "1号机柜",
          size: { w: 30, h: 70, d: 30 }, // 尺寸
          position: { x: 0, y: 0, z: 0 }, // 位置
          servers: [
            {
              uuid: "11",
              id: 11,
              name: "1号服务器",
              ip: "",
              apps: "",
              status: 0
            },
            {
              uuid: "12",
              id: 12,
              name: "2号服务器",
              ip: "",
              apps: "",
              status: 1
            },
            {
              uuid: "13",
              id: 13,
              name: "3号服务器",
              ip: "",
              apps: "",
              status: 0
            },
            {
              uuid: "14",
              id: 14,
              name: "4号服务器",
              ip: "",
              apps: "",
              status: 0
            },
            {
              uuid: "15",
              id: 15,
              name: "5号服务器",
              ip: "",
              apps: "",
              status: 0
            },
            {
              uuid: "16",
              id: 16,
              name: "6号服务器",
              ip: "",
              apps: "",
              status: 0
            },
            {
              uuid: "17",
              id: 17,
              name: "7号服务器",
              ip: "",
              apps: "",
              status: 0
            },
            {
              uuid: "17",
              id: 17,
              name: "8号服务器",
              ip: "",
              apps: "",
              status: 0
            },
            {
              uuid: "17",
              id: 17,
              name: "9号服务器",
              ip: "",
              apps: "",
              status: 0
            },
            {
              uuid: "17",
              id: 17,
              name: "10号服务器",
              ip: "",
              apps: "",
              status: 0
            }
          ]
        }
      ];
      for (var i = 0; i < model.length; i++) {
        var c = model[i];
        this.createCabinet(
          c.size.w,
          c.size.h,
          c.size.d,
          c.position.x,
          c.position.y,
