        );
      }
    },
    createFloor(w, h) {
      var texture = new THREE.ImageUtils.loadTexture(
        "./static/image/anliyulinggan.jpeg"
      );
      texture.wrapS = texture.wrapT = THREE.RepeatWrapping;
      texture.repeat.set(8, 8);
      let floorGeometry = new THREE.PlaneGeometry(w, h);
      let floorMaterial = new THREE.MeshBasicMaterial({
        map: texture,
        side: THREE.DoubleSide
      });
      floorMaterial.opacity = 1;
      floorMaterial.transparent = true;
      let floor = new THREE.Mesh(floorGeometry, floorMaterial);
      floor.position.y = 0;
      floor.rotation.x = Math.PI / 2;
      this.scene.add(floor);
    },
    // 创建墙
    createCubeWall(w, h, px, py, pz, rx, ry, rz, name) {
      var geometry = new THREE.BoxGeometry(w, h, 5);
      var meterial = new THREE.MeshBasicMaterial({
        color: 0x9cb2d1,
        opacity: 0.4,
        transparent: false,
        side: THREE.DoubleSide
      });
      var glassW = new THREE.Mesh(geometry, meterial);
      glassW.position.x = px;
      glassW.position.y = py;
      glassW.position.z = pz;
      glassW.rotation.y = ry;
      glassW.name = name;
      return glassW;
    },
    // 创建窗户
    createCubeWindow(w, h, px, py, pz, rx, ry, rz, name) {
      var geometry = new THREE.BoxGeometry(w, h, 5);
      var meterial = new THREE.MeshBasicMaterial({
        color: 0x003333,
        opacity: 0.5,
        transparent: true
      });
      var glassW = new THREE.Mesh(geometry, meterial);
      glassW.position.x = px;
      glassW.position.y = py;
      glassW.position.z = pz;
      glassW.rotation.y = ry;
      glassW.name = name;
      return glassW;
    },
    createDoor_left(width, height, depth, angle, x, y, z, name) {
      let loader = new THREE.TextureLoader();
      let _this = this;
      loader.load("./static/image/door.png", function(texture) {
        var doorgeometry = new THREE.BoxGeometry(width, height, 5);
        doorgeometry.translate(50, 0, 0);
        var doormaterial = new THREE.MeshBasicMaterial({
          map: texture,
          color: 0xffffff
        });
        doormaterial.opacity = 1.0;
        doormaterial.transparent = true;
        var door = new THREE.Mesh(doorgeometry, doormaterial);
        door.position.set(x, y, z);
        door.rotation.y += angle * Math.PI; // -逆时针旋转,+顺时针
        door.name = name;
        _this.scene.add(door);
      });
    },
    // 墙上挖门窗，通过两个几何体生成BSP对象
    createResultBsp(bsp, objectscube) {
      var material = new THREE.MeshPhongMaterial({
        color: 0x9cb2d1,
        specular: 0x9cb2d1,
        shininess: 30,
        transparent: true,
        opacity: 1
      });
      var BSP = new ThreeBSP(bsp);
      for (var i = 0; i < objectscube.length; i++) {
        var lessbsp = new ThreeBSP(objectscube[i]);
        BSP = BSP.subtract(lessbsp);
      }
      var result = BSP.toMesh(material);
      result.material.flatshading = THREE.FlatShading;
      result.geometry.computeFaceNormals(); // 重新计算几何体侧面法向量
      result.geometry.computeVertexNormals();
      result.material.needsUpdate = true; // 更新纹理
      result.geometry.buffersNeedUpdate = true;
      result.geometry.uvsNeedUpdate = true;
      return result;
    },
    createCabinet(w, height, d, px, py, pz, c) {
      let h = c.servers ? c.servers.length * 10.5 : height;
      let _this = this;
      var cabinet = {};
      // 机柜的后
      var geometry = new THREE.BoxGeometry(w, h, 2);
      var meterial = new THREE.MeshBasicMaterial({
        color: 0x777777,
        opacity: 0.2,
        transparent: true
      });

      var back = new THREE.Mesh(geometry, meterial);

      back.position.x = px;
      back.position.y = py + h / 2 + 1;
      back.position.z = pz - d / 2 + 2;

      // back.hover = function (o) {
      //   _this.hoverCabinet(o.container)
      // }
      back.container = cabinet;

      this.targetList.push(back);
      this.scene.add(back);
      cabinet.back = back;

      // 机柜的左、右
      var geometry = new THREE.BoxGeometry(w, h, 2);
      var meterial = new THREE.MeshBasicMaterial({
        color: 0x777777,
        opacity: 0.9,
        transparent: true
      });

      var left = new THREE.Mesh(geometry, meterial);
      var right = new THREE.Mesh(geometry, meterial);

      left.position.x = px + w / 2 - 1;
      left.position.y = py + h / 2 + 1;
      left.position.z = pz;
      left.rotation.y = Math.PI / 2;

      right.position.x = px - w / 2 + 1;
      right.position.y = py + h / 2 + 1;
      right.position.z = pz;
      right.rotation.y = -Math.PI / 2;

      // left.hover = function (o) {
      //   _this.hoverCabinet(o.container)
      // }
      left.container = cabinet;
      // right.hover = function (o) {
      //   _this.hoverCabinet(o.container)
      // }
      right.container = cabinet;

      this.targetList.push(left);
      this.targetList.push(right);
      this.scene.add(left);
      this.scene.add(right);

      cabinet.left = left;
      cabinet.right = right;

      // 机柜的底部、顶部
      var geometry = new THREE.BoxGeometry(w, 2, d);
      var meterial = new THREE.MeshBasicMaterial({
        color: 0x222222,
        transparent: true
      });

      var top = new THREE.Mesh(geometry, meterial);
      var bottom = new THREE.Mesh(geometry, meterial);

      top.position.x = px;
      top.position.y = py + h + 2;
      top.position.z = pz;

      bottom.position.x = px;
      bottom.position.y = py + 2;
      bottom.position.z = pz;

      // top.hover = function (o) {
      //   _this.hoverCabinet(o.container)
      // }
      top.container = cabinet;
      // bottom.hover = function (o) {
      //   _this.hoverCabinet(o.container)
      // }
      bottom.container = cabinet;

      this.targetList.push(top);
      this.targetList.push(bottom);
      this.scene.add(top);
      this.scene.add(bottom);

      cabinet.top = top;
      cabinet.bottom = bottom;

      // 机柜门
      var geometry = new THREE.BoxGeometry(w, h, 2);
      var meterial = new THREE.MeshBasicMaterial({
        color: 0x003333,
        opacity: 0.5,
        transparent: true
      });

      var front = new THREE.Mesh(geometry, meterial);

      front.position.x = px;
      front.position.y = py + h / 2 + 1;
      front.position.z = pz + d / 2;
      front.toggle = function(o) {
        if (o.rotation.y === 0) {
          o.rotation.y = o.rotation.y + (Math.PI * 3) / 5;
          o.position.x = o.position.x + w / 2 + 3;
          o.position.z = o.position.z + d / 2;

          _this.openCabinet(o.container);
        } else {
          o.rotation.y = o.rotation.y - (Math.PI * 3) / 5;
          o.position.x = o.position.x - w / 2 - 3;
          o.position.z = o.position.z - d / 2;

          // 关闭机柜门时，将机柜中的服务器收起
          for (var i = 0; i < o.container.servers.length; i++) {
            o.container.servers[i].toggle(o.container.servers[i], false);
          }

          _this.closeCabinet(o.container);
        }
      };
      // front.hover = function (o) {
      //   _this.hoverCabinet(o.container)
      // }
      front.container = cabinet;

      this.scene.add(front);
      this.targetList.push(front);

      cabinet.front = front;
      cabinet.servers = [];

      if (c && c.servers) {
        cabinet.info = c;
        for (var i = 0; i < c.servers.length; i++) {
          var server = this.createServer(
            w - 4,
            4,
            d - 4,
            px,
            py + 9 * (i + 1),
            pz + 2,
            c.servers[i].status
          ); // 服务器的厚度默认为 4， 服务器之间的间隔为 5
          server.container = cabinet;
          server.info = c.servers[i];
          cabinet.servers.push(server);
        }
      }

      return cabinet;
    },
    createServer(w, h, d, px, py, pz, statue = 0) {
      let _this = this;
      // 服务器
      // var texture = new THREE.ImageUtils.loadTexture( 'data:image/gif;base64,' );
      let ServerStatue =
        statue === 0
          ? "./static/image/server.png"
          : "./static/image/serverError.png";
      var texture = new THREE.ImageUtils.loadTexture(ServerStatue);
      texture.wrapS = texture.wrapT = THREE.RepeatWrapping;
      texture.repeat.set(1, 1);

      var texture_back = new THREE.ImageUtils.loadTexture(
        "./static/image/server-back.png"
      );
      texture_back.wrapS = texture_back.wrapT = THREE.RepeatWrapping;
      texture_back.repeat.set(1, 1);

      var geometry = new THREE.BoxGeometry(w, h, d);
      // var material = new THREE.MeshBasicMaterial( { map: texture } );
      // Create an array of materials to be used in a cube, one for each side
      var materialArray = [];
      // order to add materials: x+,x-,y+,y-,z+,z-
      materialArray.push(new THREE.MeshBasicMaterial({ color: 0xbbbbbb }));
      materialArray.push(new THREE.MeshBasicMaterial({ color: 0xbbbbbb }));
      materialArray.push(new THREE.MeshBasicMaterial({ color: 0xbbbbbb }));
      materialArray.push(new THREE.MeshBasicMaterial({ color: 0xbbbbbb }));
      materialArray.push(new THREE.MeshBasicMaterial({ map: texture }));
      materialArray.push(new THREE.MeshBasicMaterial({ map: texture_back }));
      var material = new THREE.MeshFaceMaterial(materialArray);

      var server = new THREE.Mesh(geometry, material);

      server.position.x = px;
      server.position.y = py;
      server.position.z = pz;

      server.toggle = function(o, openOrClose) {
        // 关闭同一机柜中的其他服务器
        for (let i = 0; i < o.container.servers.length; i++) {
          if (o === o.container.servers[i]) continue;
          if (o.container.servers[i].position.z === pz) {
          } else {
            o.container.servers[i].position.z =
              o.container.servers[i].position.z - d / 2;
            //
            _this.closeServer(o.container.servers[i]);
          }
        }

        if (o.position.z === pz) {
          if (openOrClose == null || openOrClose) {
            o.position.z = o.position.z + d / 2;
            // 打开服务器时，处理其他逻辑
            _this.openServer(o);
          }
        } else {
          if (openOrClose === null || !openOrClose) {
            o.position.z = o.position.z - d / 2;
            // 关上服务器时，处理其他逻辑
            _this.closeServer(o);
          }
        }
      };
      server.hover = function(o) {
        _this.hoverServer(o);
      };

      this.scene.add(server);
      this.targetList.push(server);
      return server;
    },
    onDocumentMouseMove(event) {
      // the following line would stop any other event handler from firing
      // (such as the mouse's TrackballControls)
      // event.preventDefault();
      // let _this = this
      // update the mouse variable
      this.mouse.x = (event.clientX / this.SCREEN_WIDTH) * 2 - 1;
      this.mouse.y = -(event.clientY / this.SCREEN_HEIGHT) * 2 + 1;

      // find intersections

      // create a Ray with origin at the mouse position
      //   and direction into the scene (camera direction)
      var vector = new THREE.Vector3(this.mouse.x, this.mouse.y, 1);
      this.projector.unprojectVector(vector, this.camera);
      var ray = new THREE.Raycaster(
        this.camera.position,
        vector.sub(this.camera.position).normalize()
      );

      // create an array containing all objects in the scene with which the ray intersects
      var intersects = ray.intersectObjects(this.targetList);

      // if there is one (or more) intersections
      if (intersects.length > 0) {
        // console.log("Hit @ " + toString( intersects[0].point ) );
        // change the color of the closest face.
        // intersects[ 0 ].face.color.setRGB( 0.8 * Math.random() + 0.2, 0, 0 );
        // intersects[ 0 ].object.geometry.colorsNeedUpdate = true;
      }
    },

    onDocumentMouseDown(event) {
      // the following line would stop any other event handler from firing
      // (such as the mouse's TrackballControls)
      // event.preventDefault();

      // console.log("Click.");

      // update the mouse variable
      this.mouse.x = (event.clientX / this.SCREEN_WIDTH) * 2 - 1;
      this.mouse.y = -(event.clientY / this.SCREEN_HEIGHT) * 2 + 1;

      // find intersections

      // create a Ray with origin at the mouse position
      //   and direction into the scene (camera direction)
      var vector = new THREE.Vector3(this.mouse.x, this.mouse.y, 1);
      this.projector.unprojectVector(vector, this.camera);
      var ray = new THREE.Raycaster(
        this.camera.position,
        vector.sub(this.camera.position).normalize()
      );

      // create an array containing all objects in the scene with which the ray intersects
      var intersects = ray.intersectObjects(this.targetList);

      // if there is one (or more) intersections
      if (intersects.length > 0) {
        // console.log("Hit @ " + toString( intersects[0].point ) );
        // change the color of the closest face.
        // intersects[ 0 ].face.color.setRGB( 0.8 * Math.random() + 0.2, 0, 0 );
        // intersects[ 0 ].object.geometry.colorsNeedUpdate = true;

        var o = intersects[0].object;
        if (typeof o.toggle === "function") {
          o.toggle(o);
        }
      }
    },
    // 处理服务器打开、关闭时，信息的展示和隐藏
    closeCabinet(o) {
      console.log("closeCabinet");
    },
    openCabinet(o) {
      console.log("openCabinet");
    },
    hoverCabinet(o) {
      console.log("hoverCabinet");
    },
    closeServer(o) {
      console.log("closeServer");
    },
    openServer(o) {
      console.log("openServer");
    },
    hoverServer(o) {
      console.log("hoverServer");
    }
  },
  mounted() {
    this.init();
    this.animate();

    // let _this = this
    // setInterval(() => {
    //   let model = [
    //     {
    //       'uuid': '1',
    //       'id': 1,
    //       'name': '1号机柜',
    //       'size': {w: 30, h: 70, d: 30}, // 尺寸
    //       'position': {x: 0, y: 0, z: 0}, // 位置
    //       'servers': [
    //         {'uuid': '11', 'id': 11, 'name': '1号服务器', 'ip': '', 'apps': '', 'status': Math.floor(Math.random() * 10)},
    //         {'uuid': '12', 'id': 12, 'name': '2号服务器', 'ip': '', 'apps': '', 'status': Math.floor(Math.random() * 10)},
    //         {'uuid': '13', 'id': 13, 'name': '3号服务器', 'ip': '', 'apps': '', 'status': Math.floor(Math.random() * 10)},
    //         {'uuid': '14', 'id': 14, 'name': '4号服务器', 'ip': '', 'apps': '', 'status': Math.floor(Math.random() * 10)},
    //         {'uuid': '15', 'id': 15, 'name': '5号服务器', 'ip': '', 'apps': '', 'status': Math.floor(Math.random() * 10)},
    //         {'uuid': '16', 'id': 16, 'name': '6号服务器', 'ip': '', 'apps': '', 'status': Math.floor(Math.random() * 10)},
    //         {'uuid': '17', 'id': 17, 'name': '7号服务器', 'ip': '', 'apps': '', 'status': Math.floor(Math.random() * 10)}
    //       ]
    //     }
    //   ]
    //   for (let base of model) {
    //     let item = base
    //     let c = base
    //     let w = item.size.w
    //     let d = item.size.d
    //     let px = item.position.x
